<template>
  <div class="container users-me">

    <div class="columns">
      <div class="column">
        <h1 class="title is-1" translate="yes">Verify</h1>
        <p class="">{{ email }}</p>
        <div v-if="error">
          <p class="has-text-danger" translate="yes">Activation link is not valid anymore (valid only for 2 hours)</p>
          <p>
            <span translate="yes">If more than 2 hours passed, try to</span>
            &nbsp;<router-link v-bind:to="{name:'reset'}" tag="a" translate="yes">reset your password</router-link>
          </p>
        </div>
      </div>
    </div>
    
  </div>
</template>

<script>

export default {
  name: 'UsersVerify',
  data() {
    return {
      error: false
    }
  },
  computed: {
    email: function() {
      let re = new RegExp("\-\-", 'g');
      let email = this.$route.params.id.toString().replace('---', '@').replace(re, '.');
      return email;
    }
  },
  mounted: function() {
    console.log( 'verify' );
    this.sendVerifycation();
  },
  methods: {
    sendVerifycation: function() {
      let self = this;
      let id = decodeURIComponent(this.$route.params.id);
      let hash = decodeURIComponent(this.$route.params.hash);
      this.$httpc.post("user/verify", { "email": id, "hash": hash }, { timeout: 7000 })
      .then(function (response) {
        // handle success
        console.log(response);
        // same as login
        if ( response.data.user.token ) {
          self.$store.state.user.logged = true;
          self.$store.state.user.token = response.data.user.token;
          self.$store.state.user.remember = self.remember;
          let profile = response.data.user;
          self.$store.dispatch("setUser", profile);
          if ( self.remember==true ) {
            localStorage.setItem("token", response.data.user.token);
          } else {
            sessionStorage.setItem("token", response.data.user.token);
          }
          self.$router.push({"name": "me"});
        }
      })
      .catch(function (error) {
        // handle error
        console.log(error);
        // TODO - display error message
        self.error = true;
      })
      .then(function(){
        // happens always
        if (self.$store.state.user.logged) {
          self.$router.push({"name": "me"});
        } else {
          self.error = true;
          // TODO - display error message
        }
      });
    }
  }
}
</script>
