<template>
  <nav class="panel categoryFilter">
    <p class="panel-heading" translate="yes">
      Category filter
    </p>

    <div class="panel-block">
      <p class="control has-text-centered is-size-7">
        <span v-if="countsDoNotMatch">
          <span>Filtered</span>:
          <span>{{ filteredCount }}</span>
           / 
        </span>
        <span>Total</span>:
        <span>{{ categoryTotalCount }}</span>
      </p>
    </div>

    <div class="panel-block">
      <p class="control has-icons-left">
        <input class="input is-small" type="text" translate="yes" placeholder="search" v-model.trim="tempFilter.stringFilter" v-on:keyup="stringFilterChange">
        <span class="icon is-small is-left">
          <i class="fas fa-search" aria-hidden="true"></i>
        </span>
      </p>
    </div>

    <div class="panel-block">
      <div class="panel-block-multiwrapper">
        <div>
          <label class="label" translate="yes">Price</label>
        </div>
        <div slider class="slider-range">
          <div class="slider-range-controls">
            <div inverse-left style="width:0%;"></div>
            <div inverse-right style="width:0%;"></div>
            <div range style="left:0%;right:0%;"></div>
            <span thumb="left" style="left:0%;"></span>
            <span thumb="right" style="left:100%;"></span>
            <div sign="left" style="left:0%;">
              <span class="value">{{ categoryPriceMin }}</span>
            </div>
            <div sign="right" style="left:100%;">
              <span class="value">{{ categoryPriceMax }}</span>
            </div>
          </div>
          <input type="range" position="left" tabindex="0" :value="filterPriceMin" :max="categoryPriceMax" :min="categoryPriceMin" step="1" v-on:input="priceSliderChange" />
          <input type="range" position="right" tabindex="0" :value="filterPriceMax" :max="categoryPriceMax" :min="categoryPriceMin" step="1" v-on:input="priceSliderChange" />
        </div>
      </div>
    </div>

    
    <div class="panel-block">
      <div class="panel-block-multiwrapper">
        <div>
          <label class="label"><span translate="yes">Sort by</span>:</label>
        </div>
        <div>
          <div class="control has-text-centered">
            <div class="select is-centered">
              <select v-model="tempFilter.sort" v-on:change="changeSorting">
                <option value="price">Price Ascending</option>
                <option value="-price">Price Descending</option>
                <option value="-dates.dateCreated">Latest</option>
                <option value="stockAmount">On Stock Amount</option>
              </select>
            </div>
          </div>
        </div>
      </div>
    </div>

  </nav>
</template>


<script>
import AppConstants from './../../helpers/constants'

export default {
  data () {
    return {
      eventTimeoutId: null,
      // sortingOptions: AppConstants.PRODUCT_SORTING_VALUES,
      tempFilter: {
        page: 1, 
        minMaxPrice: {
          min: 0, // minimal price of user SELECTED RANGE
          max: 0 // maximal price of user SELECTED RANGE
        },
        stringFilter: "",
        sort: AppConstants.PRODUCT_DEFAULT_SORTING
      },
      introMinMaxPrice: { // use these to 
        min: null,
        max: null
      }
    }
  },
  props: ['filter', 'categoryMinMaxPrice'],
  computed: {
    filterPriceMin: function() {
      if (this.tempFilter.minMaxPrice.min != 0) {
        return this.tempFilter.minMaxPrice.min;
      } else {
        return this.categoryPriceMin;
      }
    }, 
    filterPriceMax: function() {
      if (this.tempFilter.minMaxPrice.max != 0) {
        return this.tempFilter.minMaxPrice.max;
      } else {
        return this.categoryPriceMax;
      }
    }, 
    categoryPriceMin: function() { // minimal price for this category
      if (typeof this.categoryMinMaxPrice !== "undefined" && this.categoryMinMaxPrice.min<this.categoryMinMaxPrice.max) {
        return this.categoryMinMaxPrice.min;
      }
      return 0;
    },
    categoryPriceMax: function() { // maximal price for this category
      if (typeof this.categoryMinMaxPrice !== "undefined" && this.categoryMinMaxPrice.max>this.categoryMinMaxPrice.min) {
        return this.categoryMinMaxPrice.max;
      }
      return 0;
    },
    filteredCount: function() {
      if (typeof this.$parent !== "undefined") {
        return this.$parent.getCategoryFilteredCount();
      }
      return 0;
    },
    categoryTotalCount: function() {
      if (typeof this.$parent !== "undefined" && typeof this.$parent.categoryDetail !== "undefined" && this.$parent.categoryDetail && this.$parent.categoryDetail.count && this.$parent.categoryDetail.count>0) {
        return this.$parent.categoryDetail.count;
      }
      return 0;
    },
    countsDoNotMatch: function() {
      if (this.filteredCount>0 && this.filteredCount!=this.categoryTotalCount) {
        return true;
      }
      return false;
    }
  },
  methods: {
    stringFilterChange: function(e) {
      // create simple variables
      let self = e.target;
      this.filter.stringFilter = self.value;
      this.requestFilteredProductList();
    }, 
    priceSliderChange: function(e) {
      let forceValue = (typeof e.forceValue!=="undefined") ? e.forceValue : null;
      // create simple variables
      let self = e.target;
      let position = self.getAttribute('position');
      // create oposite variables
      let opositePosition = "";
      let opositeDifference = 0;
      let opositeRangeElement = null;
      // fill oposite variables and get value
      if (position==="left") { 
        opositePosition = "right"; 
        opositeDifference = -1;
        opositeRangeElement = self.parentNode.querySelector('input[type=range][position='+opositePosition+']');
        self.value = Math.min(self.value, parseInt(opositeRangeElement.value) + opositeDifference);
      } else { 
        position = "right";
        opositePosition = "left"; 
        opositeDifference = 1;
        opositeRangeElement = self.parentNode.querySelector('input[type=range][position='+opositePosition+']');
        self.value = Math.max(self.value, parseInt(opositeRangeElement.value) + opositeDifference);
      }
      let value = null;
      if (forceValue!==null) {
        let categoryMinMax = this.$parent.getCategoryMinMax();
        if (self.min==0  && typeof categoryMinMax.min!=="undefined") {
          self.min = categoryMinMax.min;
        }
        if (self.max==0  && typeof categoryMinMax.max!=="undefined") {
          self.max = categoryMinMax.max;
        }
        value = (100/(parseInt(self.max)-parseInt(self.min))) * parseInt(forceValue)  -  (100/(parseInt(self.max)-parseInt(self.min))) * parseInt(self.min);
        self.value = forceValue;
      } else {
        value = (100/(parseInt(self.max)-parseInt(self.min))) * parseInt(self.value)  -  (100/(parseInt(self.max)-parseInt(self.min))) * parseInt(self.min);
      }
      let childrenWrapper = self.parentNode.querySelector('.slider-range-controls');
      if (position==='left') {
        childrenWrapper.querySelector('[inverse-'+position+']').style.width = value+'%';
        childrenWrapper.querySelector('[range]').style.left = value+'%';
      } else {
        childrenWrapper.querySelector('[inverse-'+position+']').style.width = (100-value)+'%';
        childrenWrapper.querySelector('[range]').style.right = (100-value)+'%';
      }
      childrenWrapper.querySelector('[thumb='+position+']').style.left = value+'%';
      childrenWrapper.querySelector('[sign='+position+']').style.left = value+'%';
      childrenWrapper.querySelector('[sign='+position+'] .value').innerHTML = self.value;
      // create result
      let result = {};
      if (position==='left') {
        result.min = self.value;
        result.max = opositeRangeElement.value;
      } else {
        result.min = opositeRangeElement.value;
        result.max = self.value;
      }
      this.tempFilter.minMaxPrice = result;
      if (forceValue===null) {
        this.requestFilteredProductList();
      }
    }, 

    requestFilteredProductList() {
      let self = this;
      clearTimeout(this.eventTimeoutId);
      this.eventTimeoutId = setTimeout( function() {
        self.$root.loadingShow();
        self.$httpc.post("products/find", self.buildFilterRequestObject(), { timeout: 7000 })
        .then(function (response) {
          // handle success
          let categoryDetail = self.$parent.categoryDetail;
          response["data"] = { 
            categories: self.$parent.categories,
            categoryDetail: categoryDetail,
            filter: response.data.filter,
            products: response.data.products, 
            filteredProductsCount: response.data.filteredProductsCount
          };
          self.changeFilterUrl();
          self.$parent.processCategoryResponse(response);
        })
        .catch(function (error) {
          // handle error
          console.log(error);
        });
      }, 1000);
    }, 

    buildFilterRequestObject(fromParentComponent) {
      let filter = this.tempFilter;
      if (typeof fromParentComponent !== "undefined" && fromParentComponent) { filter = fromParentComponent };
      
      let categoriesFilter = null;
      // if category detail is already available, send subcategories list (AKA categoriesFilter)
      if (typeof this.$parent.categoryDetail !== "undefined" && this.$parent.categoryDetail) {
        filter['category'] = {
          _id: this.$parent.categoryDetail._id,
          pathSlug: this.$parent.categoryDetail.pathSlug,
          subsSlugs: this.$parent.categoryDetail.subsSlugs
        };
        let allCategorySlugs = filter.category.subsSlugs;
        allCategorySlugs.unshift(filter.category.pathSlug);
        // set categories if available
        categoriesFilter = {"$in": allCategorySlugs};
      }
      let query = { 
        "query": {
          "categories": categoriesFilter
        }, 
        "limit": AppConstants.PER_PAGE, 
        "sort": filter.sort
      };
      // page filter
      if ( filter.page>1 ) {
        let offset = Math.abs(filter.page-1) * AppConstants.PER_PAGE;
        if (offset>0) {
          query.offset = offset;
        }
      }
      // string filter
      if ( filter.stringFilter && filter.stringFilter.trim()!=="" ) {
        query.query['name.'+this.$store.state.language.code] = {
          "$regex": filter.stringFilter
        }
      }
      // price filter
      let minMaxPriceObjMin = (typeof filter.minMaxPrice!=="undefined" && typeof filter.minMaxPrice.min!=="undefined") ? filter.minMaxPrice.min : 0;
      let minMaxPriceObjMax = (typeof filter.minMaxPrice!=="undefined" && typeof filter.minMaxPrice.max!=="undefined") ? filter.minMaxPrice.max : 0;
      if ( minMaxPriceObjMin>this.categoryPriceMin || minMaxPriceObjMax<this.categoryPriceMax ) {
        query.query['price'] = {
          "$gte": parseFloat(minMaxPriceObjMin), 
          "$lte": parseFloat(minMaxPriceObjMax)
        };
      }
      return query;
    },

    changeFilterUrl() {
      let newUrl = this.filterObjectToQueryString();
      if (newUrl !== "") {
        history.pushState({}, null, newUrl );
      }
    },

    filterObjectToQueryString() {
      let queryStringArray = [];
      let queryString = "";
      if ( this.tempFilter.page>1 ) {
        queryStringArray.push("page="+encodeURIComponent(parseInt(this.tempFilter.page)))
      }
      if ( this.tempFilter.minMaxPrice.min > this.categoryPriceMin ) {
        queryStringArray.push("price-min="+encodeURIComponent(this.tempFilter.minMaxPrice.min))
      }
      if ( this.tempFilter.minMaxPrice.max < this.categoryPriceMax ) {
        queryStringArray.push("price-max="+encodeURIComponent(this.tempFilter.minMaxPrice.max))
      }
      if ( this.tempFilter.stringFilter.trim()!="" ) {
        queryStringArray.push("string="+encodeURIComponent(this.tempFilter.stringFilter.trim()))
      }
      if ( this.tempFilter.sort.trim()!="" && this.tempFilter.sort!=this.filter.sort ) {
        queryStringArray.push("sort="+encodeURIComponent(this.tempFilter.sort.trim()))
      }
      if (queryStringArray.length>0) {
        queryString = this.$route.path + "?" + queryStringArray.join("&");
      }
      return queryString;
    }, 

    setActivePage(page) {
      this.tempFilter.page = page;
      this.requestFilteredProductList();
    },

    changeSorting(e) {
      this.tempFilter.sort = e.target.value;
      this.requestFilteredProductList();
    }, 

    setTempFilter(newFilterValues) {
      let self = this;
      let refreshSliders = false;

      let sliderInputLeft = { 
        target: this.$el.querySelector('input[type=range][position=left]')
      };
      let sliderInputRight = { 
        target: this.$el.querySelector('input[type=range][position=right]')
      };

      Object.keys(newFilterValues).forEach(function(key){
        if ( typeof self.tempFilter[key] !== "undefined" ) {
          self.tempFilter[key] = newFilterValues[key];
          if ( key=="minMaxPrice" ) {
            sliderInputLeft["forceValue"] = self.tempFilter.minMaxPrice.min;
            sliderInputRight["forceValue"] = self.tempFilter.minMaxPrice.max;
            refreshSliders = true;
          }
        }
      });
      if ( refreshSliders ) {
        this.priceSliderChange(sliderInputLeft);
        this.priceSliderChange(sliderInputRight);
      }
    }

  }
}
</script>



