                  <div class="control">
                    <input class="input" type="text" v-model="user.username" placeholder="Username">
                  </div>
                  <div class="form-messages is-hidden">
                    <div class="form-message" data-message="empty" translate="yes">This field cannot be empty</div>
                  </div>
                </div>
              </div>
            </div>
            <div class="columns is-variable is-2 is-centered">
              <div class="column is-6">
                <div class="field email">
                  <label class="label">Email</label>
                  <div class="control">
                    <input class="input" type="email" v-model="user.email" placeholder="Email">
                  </div>
                  <div class="form-messages is-hidden">
