                  </div>
                </div>
              </div>
              <div class="column is-6">
                <div class="field email2">
                  <label class="label">Email Confirm</label>
                  <div class="control">
                    <input class="input" type="email" v-model="user.email2" placeholder="Email Confirm">
                  </div>
                  <div class="form-messages is-hidden">
                    <div class="form-message" data-message="empty" translate="yes">This field cannot be empty</div>
                    <div class="form-message" data-message="not confirmed" translate="yes">Values do not match</div>
                  </div>
                </div>
              </div>
            </div>
            <div class="columns is-variable is-2 is-centered">
              <div class="column is-6">
                <div class="field password">
                  <label class="label">Password</label>
                  <div class="control">
                    <input class="input" type="password" v-model="user.password" placeholder="Password">
                  </div>
                  <div class="form-messages is-hidden">
                    <div class="form-message" data-message="empty" translate="yes">This field cannot be empty</div>
                  </div>
                </div>
              </div>
              <div class="column is-6">
                <div class="field password2">
