<script>
export default {
  name: "UsersRegister",
  data () {
    return {
      sent: false,
      user: {
        "username": "",
        "email": "",
        "email2": "",
        "password": "",
        "password2": "",
        "nameFirst":"",
        "nameLast":"",
        "street":"",
        "street2":"",
        "zip":"",
        "city":"",
        "state":"",
        "country":"",
        "phone":""
      }
    }
  },
  created: function () {
    //this.getUsers();
  },
  methods: {
    userRegister: function() {
      let readyToSend = false;
      let fieldsPassed = 0;
      let requiredFields = ["username", "email", "password"];//, "nameFirst", "nameLast", "street", "zip", "city", "country", "phone"];
      let fieldsToConfirm = ["email", "password"];
      let allFieldsForCheck = [];
      let badFields = [];
      let self = this;

      requiredFields.forEach((field) => {
        allFieldsForCheck.push(field);
        if ( typeof this.user[field] !== "undefined" && this.user[field]!=null && this.user[field].toString().trim()!="" ) { // check if not empty
          if ( fieldsToConfirm.indexOf(field)>-1 ) {
            if ( this.user[field]===this.user[field+"2"] ) {
              fieldsPassed++;
            } else {
              badFields.push({"name": field+"2", "action": "not confirmed"});
            }
          } else {
            fieldsPassed++;
          }
        } else {
          badFields.push({"name": field, "action": "empty"});
        }
      });

      fieldsToConfirm.forEach((field) => {
        allFieldsForCheck.push(field+"2");
      });

      // frontend verification
      if ( fieldsPassed==requiredFields.length ) {
        readyToSend = true;
      }      
      if ( readyToSend ) {
        let userToSend = {
          user: {
            username: this.user.username,
            email: this.user.email,
            password: this.user.password
          }
        };
        if ( (this.$store.state.language && this.$store.state.language.code) && (this.$store.state.currency && this.$store.state.currency.code) ) {
          userToSend.user["settings"] = {
            language: this.$store.state.language.code,
            currency: this.$store.state.currency.code
          }
        }
        this.$httpc.post("users/register", userToSend, {
            timeout: 5000
        })
          .then(function (response) {
            // handle success
            console.log(response.data);
            self.sent = true;
          })
          .catch(function (error) {
            // handle error
            console.log(error);
          })
          .then(function () {
            // always executed
            this.$helpers.formUndecorateAndDecorateFields(allFieldsForCheck, badFields);
          });
      } else {
        this.$helpers.formUndecorateAndDecorateFields(allFieldsForCheck, badFields);
      }
    }

  }
}
</script>

