<template>
  <div class="container users-me">

    <div class="columns">
      <div class="column">
        <h1 class="title is-1" translate="yes">Reset password</h1>
      </div>
    </div>

    <div class="columns">
      <div v-if="!sent" class="column is-half is-offset-one-quarter">
        <form v-on:submit.prevent="resetPassword">
          <div class="">
            <div class="columns is-variable is-2 is-centered">
              <div class="column is-12">
                Required Field
              </div>
            </div>
            <div class="columns is-variable is-2 is-centered">
              <div class="column is-6">
                <div class="field email">
                  <label class="label">Email</label>
                  <div class="control">
                    <input class="input" type="email" v-model="email" placeholder="Email">
                  </div>
                  <div class="form-messages is-hidden">
                    <div class="form-message" data-message="not found" translate="yes">This email couldn't be found</div>
                  </div>
                </div>
              </div>
            </div>

            <button class="button is-success">
              <font-awesome-icon icon="check" /> &nbsp; 
              Reset
            </button>
          </div>

        </form>
      </div>
      <div v-else class="column is-half is-offset-one-quarter">
        <h2 class="title is-2" translate="yes">We are sending you email</h2>
        <p translate="yes">Check your mailbox for verification email (if not found check, also you spam folder).</p>
      </div>
    </div>
    
  </div>
</template>

<script>

export default {
  name: 'UsersVerify',
  data() {
    return {
      email: null,
      sent: false
    }
  },
  computed: {
  },
  mounted: function() {
    console.log( 'reset' );
  },
  methods: {
    resetPassword: function() {
      let self = this;
      this.$httpc.post("user/reset", { email: this.email }, { timeout: 7000 })
      .then(function (response) {
        // handle success
        console.log(response);
        self.sent = true;
      })
      .catch(function (error) {
        // handle error //
        if ( error.response && error.response.data && error.response.data.errors && Object.keys(error.response.data.errors).length>0 ) {
          // TODO - find out how to create global function for this
          // decorate form fields with errors
          Object.keys(error.response.data.errors).forEach(function (item) {
            let element = self.$el.querySelector('.field.'+item);
            if ( element ) {
              let messageElement = element.querySelector('.form-messages .form-message[data-message="'+error.response.data.errors[item]+'"]');
              if (messageElement && messageElement!=null) {
                self.$helpers.undecorateFormField(element, 'danger');
                let message = messageElement.innerText;
                self.$helpers.decorateFormField(element, message, 'danger');
              }
            }
          });
        }
      })
      .then(function(){
        // happens always
      });
    }
  }
}
</script>
