<template>
  <div id="meInfo" class="" v-if="$store.state.user.profile">
    <div class="columns is-variable is-2">
      <div class="column is-8 has-text-centered">
        <h2 class="title is-2 has-text-centered">{{ $store.state.user.profile.username }}</h2>
        <!-- <p class="has-text-centered" v-if="$store.state.user.profile.addressInvoice">
          AKA 
          {{ $store.state.user.profile.addressInvoice.nameFirst }} 
          {{ $store.state.user.profile.addressInvoice.nameLast }}
        </p> -->
      </div>
    </div>

    <div class="columns is-variable is-2">
        <div class="column is-4 has-text-centered">
          <div class="user--avatar-wrapper is-centered" :style="{ 'background-image': 'url(\''+userProfileImage+'\')' }"></div>
        </div>
        <div class="column is-4 has-text-centered">
          <form v-on:submit.prevent="uploadUserProfileImage">
            <div class="field">
                <b-upload v-model="dropFiles" drag-drop>
                    <section class="section">
                        <div class="content has-text-centered">
                            <p>
                              <font-awesome-icon icon="file-upload" class="fa-3x" />
                            </p>
                            <p class="dropFilesOrClick" translate="yes">Drop your files here or click to upload</p>
                        </div>
                    </section>
                </b-upload>
            </div>

            <div class="tags">
                <span v-for="(file, index) in dropFiles"
                    :key="index"
                    class="tag is-primary" >
                    {{file.name}}
                    <button class="delete is-small"
                        type="button"
                        @click="deleteDropFile(index)">
                    </button>
                </span>
            </div>

            <button type="submit" class="button is-success">
              <span translate="yes">Change Image</span>
            </button>
          </form>
        </div>
    </div>

    
    <div class="user-info-edit-form-wrapper">
      <div class="columns">
        <div class="column is-8 is-centered">
          
          <div class="columns is-variable is-2 is-centered">
            <div class="column is-12 has-text-centered">
              <form v-on:submit.prevent="changeUsername">
                <div class="field username">
                  <label class="label" translate="yes">Username</label>
                  <div class="control">
                    <input class="input" type="text" v-model="userBasicData.username" placeholder="Username">
                  </div>
                  <div class="form-messages is-hidden">
                    <div class="form-message" data-message="exists" translate="yes">Username already exists</div>
                  </div>
                </div>
                <button class="button is-success">
                  <span translate="yes">Change Username</span>
                </button>
              </form>
            </div>
          </div>

          <div class="columns">
            <div class="column is-12">
              <hr>
            </div>
          </div>

          <form v-on:submit.prevent="changeEmail">
            <div class="columns is-variable is-2 is-centered">
              <div class="column is-6">
                <div class="field email">
                  <label class="label">Email</label>
                  <div class="control">
                    <input class="input" type="text" v-model="userBasicData.email" placeholder="Email">
                  </div>
                  <div class="form-messages is-hidden">
                    <div class="form-message" data-message="exists" translate="yes">Email already exists</div>
                  </div>
                </div>
              </div>
              <div class="column is-6">
                <div class="field email2">
                  <label class="label" translate="yes">Email Confirm</label>
                  <div class="control">
                    <input class="input" type="text" v-model="userBasicData.email2" placeholder="Email Confirm">
                  </div>
                  <div class="form-messages is-hidden">
                    <div class="form-message" data-message="not confirmed" translate="yes">Values do not match</div>
                  </div>
                </div>
              </div>
            </div>
            <div class="columns is-variable is-2 is-centered">
              <div class="column is-12 has-text-centered">
                <button class="button is-success">
                  <span translate="yes">Change your Email</span>
                </button>
              </div>
            </div>
          </form>

          <div class="columns">
            <div class="column is-12">
              <hr>
            </div>
          </div>

          <form v-on:submit.prevent="changePassword">
            <div class="columns is-variable is-2 is-centered">
              <div class="column is-6">
                <div class="field password">
                  <label class="label" translate="yes">Password</label>
                  <div class="control">
                    <input class="input" type="password" v-model="userBasicData.password" placeholder="Password">
                  </div>
                </div>
              </div>
              <div class="column is-6">
                <div class="field password2">
                  <label class="label" translate="yes">Password Confirm</label>
                  <div class="control">
                    <input class="input" type="password" v-model="userBasicData.password2" placeholder="Password Confirm">
                  </div>
                  <div class="form-messages is-hidden">
                    <div class="form-message" data-message="not confirmed" translate="yes">Values do not match</div>
                    <div class="form-message" data-message="unknown error" translate="yes">An error occured when changing password</div>
                  </div>
                </div>
              </div>
            </div>
            <div class="columns is-variable is-2 is-centered">
              <div class="column is-12 has-text-centered">
                <button class="button is-success">
                  <span translate="yes">Change your Password</span>
                </button>
              </div>
            </div>
          </form>

        </div>
      </div>

    </div> <!-- user-info-edit-form-wrapper -->
  </div>
</template>

<script>
export default {
  name: 'UserMeInfo',
  data() {
      return {
          dropFiles: []
      }
  },
  computed: {
    userBasicData: function() {
      let user = this.$store.getters.getUserProfile;
      console.log(user);
      let result = {
        "username": "",
        "email": "",
        "email2": "",
        "password": "",
        "password2": "",
      };

      if ( user && user.username ) {
        result.username = user.username;
        result.email = user.email;
      }

      return result;
    },
    userProfileImage: function() {
      return this.$store.getters.getProfileImage();
    }
  },
  methods: {
    deleteDropFile(index) {
        this.dropFiles.splice(index, 1)
    }, 

    uploadUserProfileImage: function() {
      let self = this;
      let formData = new FormData();
      if ( this.dropFiles.length && this.dropFiles.length>0 ) {
        for( var i = 0; i < this.dropFiles.length; i++ ){
          let file = this.dropFiles[i];
          formData.append('files[' + i + ']', file);
        }
      } else {
        formData.append('files[0]', this.dropFiles);
      }

      // check if new username is available
      this.$httpc.post("user/image",
        formData,
        { headers: {
            "Authorization": "Token "+this.$store.state.user.token,
            "Content-Type": "application/json",
            "Accept": "application/json"
        } }
      )
        .then(function (response) {
          // handle success in some ugly response
          if ( response && response.data ) {
            if ( response.data.substr(0,1)=="'" && response.data[response.data.length-1]=="'" ) {
              let templen = response.data.length-2;
              response.data = response.data.substr(1,templen);
              response.data = JSON.parse(response.data);
            }
            if ( response.data.files && response.data.files.length>0 && response.data.files[0].path ) {
              self.$store.dispatch("setUserProfileImage", response.data.files[0].path);
              self.$forceUpdate();
            }
          }
        })
        .catch(function (error) {
          // handle error
          console.log(error);
        })
        .then(function () {
          // always executed
        });
    },

    changeUsername: function() {
      let status = 'send';
      let errorMsgs = [];
      let sendChangeRequest = true;
      let self = this;

      // check if new username is available
      this.$httpc.post("users/checkusername",{ username: this.userBasicData.username })
        .then(function (response) {
          // handle success
          if ( response.data && response.data==true ) {
            sendChangeRequest = false;
            status = 'error';
            errorMsgs.push({target: 'username', action: 'already exists'});
          } else {
            sendChangeRequest = true;
            
            if ( sendChangeRequest ) {
              // udpate user username
              self.$store.dispatch("updateUser", {username: self.userBasicData.username});
            }
          }
          console.log(errorMsgs);
        })
        .catch(function (error) {
          self.sendChangeRequest = false;
          self.status = 'error';
          // handle error
          if ( error.response.data && error.response.data.errors ) {
            self.$helpers.showFormFieldErrorsFromErrorResponse(error.response.data.errors);
          }
        })
        .then(function () {
          // always executed
        });
      // update user username
    },

    changeEmail: function() {
      let status = 'send';
      let errorMsgs = [];
      let sendChangeRequest = true;
      let self = this;

      // check if email & email2 are same
      if ( !this.userBasicData.email || !this.userBasicData.email2 || this.userBasicData.email!==this.userBasicData.email2 ) {
        status = 'error';
        self.$helpers.formUndecorateAndDecorateFields(["email2"],[{name: 'email2', action: 'not confirmed'}]);
        sendChangeRequest = false;
      }

      if (sendChangeRequest) {
        // check if new email is available
        this.$httpc.post("users/checkemail",{ email: this.userBasicData.email })
        .then(function (response) {
          // handle success
          sendChangeRequest = true;
          
          if ( sendChangeRequest ) {
            self.$store.dispatch("updateUser", {email: self.userBasicData.email});
          }
        })
        .catch(function (error) {
          self.sendChangeRequest = false;
          self.status = 'error';
          // handle error
          if ( error.response.data && error.response.data.errors ) {
            self.$helpers.showFormFieldErrorsFromErrorResponse(error.response.data.errors);
          }
        })
        .then(function () {
          // always executed
        });
      }
    },

    changePassword: function() {
      let status = 'send';
      let errorMsgs = [];
      let sendChangeRequest = true;
      let self = this;

      // check if password & password2 are same
      console.log(this.userBasicData.password +" !== "+ this.userBasicData.password2 , this.userBasicData.password!==this.userBasicData.password2)
      if ( !this.userBasicData.password || !this.userBasicData.password2 || this.userBasicData.password!==this.userBasicData.password2 ) {
        status = 'error';
        self.$helpers.formUndecorateAndDecorateFields(["password2"],[{name: 'password2', action: 'not confirmed'}]);
        sendChangeRequest = false;
      }

      /* if (sendChangeRequest) {
        // check if new email is available
        this.$httpc.put("user",{ user: { password: this.userBasicData.password } })
        .then(function (response) {
          // handle success
          sendChangeRequest = true;
          
          if ( sendChangeRequest ) {
            self.$store.dispatch("updateUser", {email: self.userBasicData.email});
          }
        })
        .catch(function (error) {
          self.sendChangeRequest = false;
          self.status = 'error';
          // handle error
          console.log(error);
          if ( error.response.data && error.response.data.errors ) {
            self.$helpers.formUndecorateAndDecorateFields(["password2"],[{name: 'password2', action: 'unknown error'}]);
          }
        })
        .then(function () {
          // always executed
        });
      } */
    }
  }
}
</script>


