<template>
  <div class="container product-detail">
    <div class="column">
      <h1 class="title" v-if="this.product">{{ this.product.name.en }}</h1>
      <h2 class="subtitle" v-if="this.product">{{ this.product.descriptionShort.en }}</h2>
      <p v-if="this.product">{{ this.product.descriptionLong.en }}</p>
      <p><span translate="yes">In stock</span> {{ this.product.stockAmount }}</p>
      <div>
        <button class="button is-link" v-bind:data-id="product._id" @click.prevent="addToCart">
          <font-awesome-icon icon="cart-plus" /> &nbsp; <span translate="yes">Add to Cart</span>
        </button>
      </div>
    </div>
  </div>
</template>

<script>
export default {
  name: "ProductDetail",
  data () {
    return {
      product: null,
      language: "en"
    }
  },
  created: function () {
    // this.language = this.$store.state.language.code;
    this.getProductDetail();
  },
  methods: {
    getProductDetail: function() {
      let self = this;
      console.log( 'detail', this.$route.params );
      if ( this.$route.params && 
          this.$route.params.category && this.$route.params.category.toString().trim()!='' && 
          this.$route.params.product && this.$route.params.product.toString().trim()!='' ) {
        this.$httpc.get("products/"+this.$route.params.category+"/detail/"+this.$route.params.product)
          .then(function (response) {
            // handle success
            if ( response.data ) {
              self.product = response.data;
              console.log(response.data);
            }
            if ( response.data.products ) {
              self.products = response.data.products;
            }
            if ( response.data.categoryDetail ) {
              self.categoryDetail = response.data.categoryDetail;
            }
          })
          .catch(function (error) {
            // handle error
            console.log(error);
          })
          .then(function () {
            // always executed
          });
      }
    }
  }
}
</script>

