/**
 * these are the global helper functions
 */

export const helpers = {
  checkIfObjectInArray: (checkObject, checkArray) => {
    if (checkObject && checkObject!=null && checkArray && checkArray instanceof Array && checkArray.length>0) {
      for (let i=0; i<checkArray.length; i++) {
        if ( JSON.stringify(checkArray[i])==JSON.stringify(checkObject) ) {
          return checkObject;
        }
      }
    }
    return null;
  },
  /**
   * Decorate form field with color, icon and message. The recomende format for email form field would be:
   * 
<div class="field email"> // "email" is one name for both - this field and for property that returns in error response JSON
	<div class="control">
		<input class="input" placeholder="Email" type="email" v-model.trim="email" />
	</div>
	<div class="form-messages is-hidden">
		<div class="form-message" data-message="is not found">Email does not exist</div> // "is not found" is error code, that is used in JSON response
	</div>
</div>
   *
   * JSON response would look like this:
{
  "errors": {
    "email": "is not found"
  }
}
   * 
   * 
   * @param {HTMLelement} element - div element with class="field"
   * @param {string} errorMessage - message to display under control
   * @param {string} type - type of form information: danger / success / warning
   */
  decorateFormField: (element, errorMessage, type) => {
    if (errorMessage && errorMessage!=null) {
      // set icon (and just for case even color)
      let typeIconClass = 'exclamation-circle'; // danger
      let typeColorClass = 'isRed';
      switch (type) {
        case "success":
          typeIconClass = 'check';
          typeColorClass = 'isGreen';
          break;
        case "warning":
          typeIconClass = 'exclamation-triangle';
          typeColorClass = 'isYellow';
          break;
      }
      // REMEMBER to remove old decoration before any action using this.$helpers.decorateFormField(element, errorMessage, type);
      let control = element.querySelector('.control');
      if (control && control!=null) {
        control.className += " has-icons-right";
        control.querySelector('.input').className += " is-"+type;
        let icon = document.createElement('span');
        icon.className = 'icon is-right has-text-'+type;
        icon.innerHTML = '<i class="fa fa-'+typeIconClass+'"></i>';
        control.appendChild(icon);
      }
      let text = document.createElement('p');
      text.className = 'help is-'+type;
      text.innerHTML = errorMessage;
      element.appendChild(text);
    }
  },
  /**
   * Removes decoration from element
   * use before calling this.$helpers.decorateFormField(element, message, 'danger') 
   * if you don't want multiple lines of messages
   * 
   * @param {HTMLelement} element 
   * @param {string} element 
   */
  undecorateFormField(element, type) {
    let control = element.querySelector('.control');
    if (control && control!=null) {
      control.classList.remove('has-icons-right');
      control.querySelector('.input').classList.remove('is-'+type);
      let icon = control.querySelector('span'+'.icon.is-right.has-text-'+type)
      if (icon && icon!=null) {
        console.log(icon);
        control.removeChild(icon)
      }
    }
    let text = element.querySelector('p'+'.help.is-'+type);
    if (text && text!=null) {
      element.removeChild(text);
    }
  },


  /**
   * Simple function to split string into chunks
   * 
   * @param {string} str 
   * @param {int} chunkSize 
   */
  stringChunk(str, chunkSize) {
    chunkSize = (typeof chunkSize === 'undefined') ? 2 : chunkSize;
    let resultString = '';

    if ( str.length>0 ) {
      let resultArray = [];
      let chunk = '';
       for ( let i = 0; i<str.length; i=(i+chunkSize) ) {
         chunk = str.substring(i,i+chunkSize);
         if ( chunk.trim()!='' ) {
           resultArray.push(chunk);
         }
       }
       if (resultArray.length) {
         resultString = resultArray.join('/');
       }
    } else {
      resultString = str;
    }

    return resultString;
  }, 

  /**
   * undecorate and decorate fields
   * 
   * @param {array} undecorateFields - simple array of field class strings to undecorate ["username", "password", ...]
   * @param {array} decorateFields - array of objects that represent name and action to decorate [{"name":"username", "action":"empty"}, ...]
   */
  formUndecorateAndDecorateFields: function(undecorateFields, decorateFields) {
    let self = this;
    undecorateFields.forEach((field) => {
      let element = document.querySelector('.field.'+field);
      if ( element ) {
        self.undecorateFormField(element, 'danger');
      }
    });

    Object.keys(decorateFields).forEach(function (key) {
      let element = document.querySelector('.field.'+decorateFields[key].name);
      if ( element ) {
        let messageElement = element.querySelector('.form-messages .form-message[data-message="'+decorateFields[key].action+'"]');
        if (messageElement && messageElement!=null) {
          let message = messageElement.innerText;
          self.decorateFormField(element, message, 'danger');
        }
      }
    });
  },


  /**
   * 
   * @param {Object} errors - value of error response as represented in error.response.data.errors
   */
  showFormFieldErrorsFromErrorResponse: function(errors) {
    let self = this;
    let undecorate = [];
    let decorate = [];
    Object.keys(errors).forEach(function(key) {
      let name = key;
      let action = errors[key];
      undecorate.push(key);
      decorate.push({"name":name, "action":action});
    });
    if ( undecorate.length>0 ) {
      this.formUndecorateAndDecorateFields(undecorate, decorate);
    }
  }
  
}