<template>
    <article class="tile is-child box">
      <p class="title">{{ product.name[$store.state.language.code] }}</p>
      <p class="subtitle">{{ product.descriptionShort[$store.state.language.code] }}</p>
      <div class="tile-image">
        <img v-bind:src="getProductImagePath(product.orderCode)" v-bind:alt="product.name[$store.state.language.code]" />
      </div>
      <p><span translate="yes">In stock</span> {{ product.stockAmount }}</p>
      <p><span translate="yes">Price</span> {{ $store.state.numeral(product.price).format('0 0.00') }} {{ $store.state.currency.symbol }}</p>
      <div>
        <button class="button is-link" v-bind:data-id="product._id" @click.prevent="addToCart">
          <font-awesome-icon icon="cart-plus" /> &nbsp; <span translate="yes">Add to Cart</span>
        </button>
      </div>
    </article>
</template>


<script>
export default {
  props: ['product'],
  methods: {
    addToCart: function(e) {
      let self = this;
      let productId = e.currentTarget.getAttribute('data-id');
      let amount = e.currentTarget.getAttribute('data-amount');

      this.animateAddToCart(e.currentTarget);
      if (!amount) {
        amount = 1;
      }
      console.log(productId);
      this.$httpc.put("cart",{
        "itemId": productId,
	      "amount": amount
      })
      .then(function (response) {
        console.log(response);
        if (response.data) {
          self.$store.dispatch('setCart',response.data);
        }
      })
      .catch(function (error) {
        // handle error
        console.log(error);
      })
      .then(function () {
        // always executed
      });
    },
    animateAddToCart: function(currentTarget) {
      function animate({timing, draw, duration}) {
        let start = performance.now();

        requestAnimationFrame(function animate(time) {
          // timeFraction goes from 0 to 1
          let timeFraction = (time - start) / duration;
          if (timeFraction > 1) timeFraction = 1;

          // calculate the current animation state
          let progress = timing(timeFraction);

          draw(progress); // draw it

          if (timeFraction < 1) {
            requestAnimationFrame(animate);
          }

        });
      }

      let cart = document.querySelector('.userCartLink');
      let cartCountCount = document.querySelector('.userCartLink .cartItemsCount');
      let cartCount = (typeof cartCountCount !== "undefined" && cartCountCount) ? parseInt(cartCountCount.textContent, 10) : 0;
      console.log(cartCount);

      let cartX = cart.getBoundingClientRect().left + document.body.scrollLeft,
          cartY = cart.getBoundingClientRect().top + document.body.scrollTop;
      cartX = Math.max(cartX, 0);
      cartY = Math.max(cartY, 0);
    
      let el = currentTarget,
          item = el.parentNode.parentNode,
          img = item.querySelector('.tile-image img'),
          addToCartButt = currentTarget,
          itemToFlyFrom = img;
          console.log(itemToFlyFrom);
    
      let flyingToCart = document.createElement("img");
      flyingToCart.className = "b-flying-img";
      flyingToCart.setAttribute("src", img.getAttribute("src"));
      let imgDims = img.getBoundingClientRect();
      flyingToCart.style.width = imgDims.width;
      flyingToCart.style.height = imgDims.height;
      let imgX = itemToFlyFrom.getBoundingClientRect().left + document.body.scrollLeft;
      imgX = Math.max(imgX, 0);
      let imgY = itemToFlyFrom.getBoundingClientRect().top + document.body.scrollTop;
      imgY = Math.max(imgY, 0);
      let ratioX = ((imgX - cartX) / 100) / 100;
      let ratioY = ((imgY - cartY) / 100) / 100;
      /*
      let ratioX = 0;
      if ( imgX > cartX ) {
        ratioX = ((imgX - cartX) / 100) / 100;
      } else {
        let tempX = Math.abs(cartX);
        ratioX = (((imgX+tempX) - (cartX+tempX)) / 100) / 100;
      }
      let ratioY = 0;
      if ( imgY > cartY ) {
        ratioY = ((imgY - cartY) / 100) / 100;
      } else {
        let tempX = Math.abs(cartX);
        ratioY = ((cartY - imgY) / 100) / 100;
      }
      */
    
      cartCount += 1;
    
      // animation types
      function linear(timeFraction) {
        return timeFraction;
      }
      function circ(timeFraction) {
        return 1 - Math.sin( Math.acos(timeFraction) );
      }
      console.log("imgX, imgY, cartX, cartY, ratioX, ratioY", [imgX, imgY, cartX, cartY, ratioX, ratioY]);
    
      animate({
        duration: 500,
        timing: circ,
        draw: function(progress) {
          flyingToCart.style.top = ((100 * 100 * (1-progress) * ratioY) - imgY + cartY) + 'px'; // update here, not -imgY +cartY if cart is lower
          flyingToCart.style.left = ((100 * 100 * (1-progress) * ratioX) - imgX + cartX) + 'px'; // update here, not -imgY +cartY if cart is on right
          flyingToCart.style.opacity = (1-progress);
          flyingToCart.style.transform = "scale(" + (1-progress) + ")";
          console.log("left, top, opacity, scale", [flyingToCart.style.left, flyingToCart.style.top, flyingToCart.style.opacity, flyingToCart.style.transform]);
          if (progress===1) {
              flyingToCart.remove();
          }
        }
      });
    
      el.parentNode.appendChild(flyingToCart);
    
      cartCountCount.textContent = cartCount;
    },
    getProductImagePath(serialPath) {
      let url = "https://stretchshop.app/static/";
      let chunk = this.$helpers.stringChunk(serialPath, 3);

      if ( this.$store.state.coredata && this.$store.state.coredata.settings && this.$store.state.coredata.settings.assets && this.$store.state.coredata.settings.assets.url ) {
        url = this.$store.state.coredata.settings.assets.url
      }

      return url+"/products/"+chunk+"/"+serialPath+"-default.jpg";
    }
  }
}
</script>

<style lang="scss">
.tile {
  position: relative;
  .tile-image {
    position: relative;
    img {
      max-height: 200px;
    }
  }
}

.b-flying-img {
  position: absolute;
  width: 200px;
  height: 200px;
  top: 0;
  left: 0;
  z-index: 5;
  transform-origin: top left;
}
</style>

