<template>
  <div class="container users-me">

    <div v-if="isLogged">
      <div class="columns">
        <div class="column">
          <h1 class="title is-1" translate="yes">Profile</h1>
        </div>
      </div>

      <div class="columns is-variable is-8 has-text-left">
        <div class="column is-3">
          <nav class="panel user-settings-nav">
            <p class="panel-heading">
              <span translate="yes">Your Settings</span>
            </p>
            <a class="panel-block" @click="userComponent('UserMeInfo', $event)">
              <span translate="yes">Basic information</span>
            </a>
            <a class="panel-block" @click="userComponent('UserMeInvoince', $event)">
              <span translate="yes">User address</span>
            </a>
            <a class="panel-block" @click="userComponent('UserMeDelivery', $event)">
              <span translate="yes">Delivery addresses</span>
            </a>
          </nav>
        </div>
        <div class="column is-9">
          <component v-bind:is="activeComponent"></component>
        </div>
      </div>
    </div>
    <div v-else>
      <div class="columns">
        <div class="column">
          <h1 class="title is-1" translate="yes">Unauthorized</h1>
          <p>
            <span translate="yes">It may help if you</span>&nbsp;<router-link v-bind:to="{name:'login'}" tag="a" translate="yes">log in</router-link>
          </p>
        </div>
      </div>
    </div>
    
  </div>
</template>

<script>
import UserMeInfo from "./MeInfo.vue";
import UserMeInvoince from "./MeInvoice.vue";
import UserMeDelivery from "./MeDelivery.vue";

export default {
  name: 'UsersMe',
  components: {
    "UserMeInfo": UserMeInfo, 
    "UserMeInvoince": UserMeInvoince, 
    "UserMeDelivery": UserMeDelivery
  },
  data() {
    return {
      activeComponent: UserMeInfo,
      menuStates: {
        "UserMeInfo": true, 
        "UserMeInvoince": false, 
        "UserMeDelivery": false
      }
    }
  },
  computed: {
    isLogged: function() {
      if ( typeof this.$store !== "undefined" && typeof this.$store.state.user.logged !== "undefined" ) {
        return this.$store.state.user.logged;
      }
      return false;
    }
  },
  created: function() {
    console.log( this.$options.components );
  },
  methods: {
    userComponent: function(componentName, event) {
      if ( this.$options.components && this.$options.components[componentName] ) {
        this.activeComponent = this.$options.components[componentName];
      }
      let listItems = this.$el.querySelectorAll('.users-me nav.user-settings-nav a.panel-block');
      if ( listItems && listItems.length ) {
        listItems.forEach(element => {
          element.classList.remove('is-active');
        });
      }
      event.target.classList.add("is-active");
    }
  }
}
</script>
