<template>
  <div class="" v-if="$store.state.user.profile">
    <h2 class="title is-2">Invoice Address</h2>

    <div class="columns"><div class="column is-8 is-centered">

      <form v-on:submit.prevent="saveUserAddresses">
        <div class="deliveryAddressWrapper">
          <div class="columns is-variable is-2">
            <div class="column is-6">
              <div class="field">
                <label class="label">First Name</label>
                <div class="control">
                  <input class="input" type="text" v-model="activeInvoiceAddress.nameFirst" placeholder="First Name">
                </div>
              </div>
            </div>
            <div class="column is-6">
              <div class="field">
                <label class="label">Last Name</label>
                <div class="control">
                  <input class="input" type="text" v-model="activeInvoiceAddress.nameLast" placeholder="Last Name">
                </div>
              </div>
            </div>
          </div>

          <div class="columns is-variable is-2">
            <div class="column is-12">
              <div class="field">
                <label class="label">Street</label>
                <div class="control">
                  <input class="input" type="text" v-model="activeInvoiceAddress.street" placeholder="Street">
                </div>
              </div>
            </div>
          </div>

          <div class="columns is-variable is-2">
            <div class="column is-12">
              <div class="field">
                <label class="label">Street 2</label>
                <div class="control">
                  <input class="input" type="text" v-model="activeInvoiceAddress.street2" placeholder="Street 2">
                </div>
              </div>
            </div>
          </div>

          <div class="columns is-variable is-2">
            <div class="column is-4">
              <div class="field">
                <label class="label">ZIP</label>
                <div class="control">
                  <input class="input" type="text" v-model="activeInvoiceAddress.zip" placeholder="ZIP">
                </div>
              </div>
            </div>
            <div class="column is-8">
              <div class="field">
                <label class="label">City</label>
                <div class="control">
                  <input class="input" type="text" v-model="activeInvoiceAddress.city" placeholder="City">
                </div>
              </div>
            </div>
          </div>

          <div class="columns is-variable is-2">
            <div class="column is-6">
              <div class="field">
                <label class="label">State</label>
                <div class="control">
                  <input class="input" type="text" v-model="activeInvoiceAddress.state" placeholder="State">
                </div>
              </div>
            </div>
            <div class="column is-6">
              <div class="field">
                <label class="label">Country</label>
                <div class="control">
                  <input class="input" type="text" v-model="activeInvoiceAddress.country" placeholder="Country">
                </div>
              </div>
            </div>
          </div>

          <div class="columns is-variable is-2 is-centered">
            <div class="column is-6">
              <div class="field">
                <label class="label">Phone</label>
                <div class="control">
                  <input class="input" type="text" v-model="activeInvoiceAddress.phone" placeholder="Phone">
                </div>
              </div>
            </div>
          </div>

          <div class="columns is-variable is-2 is-centered">
            <div class="column has-text-centered">
              <button class="button is-success">
                <span class="icon">
                  <i class="fas fa-home"></i>
                </span>
                Set Invoice Address
              </button>
            </div>
          </div>
        </div>
      </form>

    </div></div>

  </div>
</template>

<script>
export default {
  name: 'UserMeInvoice',
  data () {
    return {
      activeInvoiceAddress: {
        "type":"invoice",
        "nameFirst":"",
        "nameLast":"",
        "street":"",
        "street2":"",
        "zip":"",
        "city":"",
        "country":"",
        "phone":""
      }
    }
  },
  mounted: function() {
      // get address from store
      let userProfile = this.$store.getters.getUserProfile;
      let invoiceAddressStored;
      if ( userProfile.addressInvoice ) {
        invoiceAddressStored = userProfile.addressInvoice;
      }
      // set default value before any processing
      let result = {
        "type":"invoice",
        "nameFirst":"",
        "nameLast":"",
        "street":"",
        "street2":"",
        "zip":"",
        "city":"",
        "country":"",
        "phone":""
      };
      // if there is any value from store, use it in result
      if ( invoiceAddressStored && invoiceAddressStored.nameFirst ) {
        result = invoiceAddressStored;
      }
      // if applicaple, use latest values from form
      if ( this.activeInvoiceAddress ) {
        for (var property in this.activeInvoiceAddress) {
          if (this.activeInvoiceAddress.hasOwnProperty(property) && property.trim()!='') {
              this.activeInvoiceAddress[property] = result[property];
          }
        }
      }
      this.$forceUpdate();

      return result;
  },
  methods: {
    saveUserAddresses: function() {
      let addresses = [];
      console.log("userAddressInvoice:", this.activeInvoiceAddress);
      addresses.push(this.activeInvoiceAddress);
      addresses.push(this.$store.state.user.profile.addressesDelivery);
      this.$store.dispatch("updateUser", { "addresses": addresses } );
    }
  }
}
</script>
