import Vue from 'vue';
import Vuex from 'vuex';
import numeral from 'numeral';

Vue.use(Vuex);

export const store = new Vuex.Store({
  namespace: true,
  state: {
    "user": {
      "logged": false,
      "token": null,
      "remember": false,
      "profile": null,
      "avatar": null
    },
    "language": {
      code: "en",
      longCode: "en-US",
      name: "English"
    },
    "currency": {
      code: "EUR",
      symbol: "",
      ratio: 1
    },
    "coredata": null,
    "cart": null, 
    "order": null,
    "chunk": function(array, chunkSize) {
        var R = [];
        for (var i=0; i<array.length; i+=chunkSize)
            R.push(array.slice(i,i+chunkSize));
        return R;
    }, 
    "numeral": numeral,
    "translates": null
  },
  getters: {
    getCart: (state) => {
      if ( typeof state.cart !== 'undefined' && state.cart && state.cart!=null ) {
        return state.cart;
      }
      return null;
    },
    getUserProfile: (state) => {
      if ( typeof state.user !== 'undefined' && state.user && state.user!=null && state.user.profile && state.user.profile!=null ) {
        if ( !state.user.profile.addressInvoice || state.user.profile.addressInvoice==null ) {
          state.user.profile.addressInvoice = {
            "type":"invoice",
            "nameFirst":"",
            "nameLast":"",
            "street":"",
            "street2":"",
            "zip":"",
            "city":"",
            "state":"",
            "country":"",
            "phone":"",
            "email":""
          };
        }
        return state.user.profile;
      }
      return {
        "username":"",
        "email":"",
        "bio":"",
        "image":"",
        "addressInvoice":{
          "type":"invoice",
          "nameFirst":"",
          "nameLast":"",
          "street":"",
          "street2":"",
          "zip":"",
          "city":"",
          "state":"",
          "country":"",
          "phone":"",
          "email":""
        },
        "addressesDelivery":[]
      };
    },
    getUserInvoiceAddress: (state) => {
      if ( typeof state.user !== 'undefined' && state.user && state.user!=null && state.user.profile && state.user.profile!=null ) {
        if ( !state.user.profile.addressInvoice || state.user.profile.addressInvoice==null ) {
          state.user.profile.addressInvoice = {
            "type":"invoice",
            "nameFirst":"",
            "nameLast":"",
            "street":"",
            "street2":"",
            "zip":"",
            "city":"",
            "state":"",
            "country":"",
            "phone":"",
            "email":""
          };
        }
        return state.user.profile.addressInvoice;
      }
      return {
        "type":"invoice",
        "nameFirst":"",
        "nameLast":"",
        "street":"",
        "street2":"",
        "zip":"",
        "city":"",
        "state":"",
        "country":"",
        "phone":"",
        "email":""
      };
    },
    getOrder: (state) => {
      if ( typeof state.order !== 'undefined' && state.order && state.order!=null ) {
        return state.order;
      }
      return null;
    },
    getLanguage: (state) => {
      if ( typeof state.language !== 'undefined' && state.language && state.language!=null && state.language.code ) {
        return state.language.code;
      }
      return null;
    },
    getTranslates: (state) => {
      return state.translates;
    },
    getProfileImage: state => () => {
      return state.user.avatar;
    }

  },
  mutations: {
    setCart: (state, payload) => {
      state.cart = payload;
    }, 
    setUserInvoiceAddress: (state, payload) => {
      if (!state.user.profile) {
        state.user.profile = {};
      }
      state.user.profile.addressInvoice = payload;
      if (state.user.profile.email=='' && payload.email!='') {
        state.user.profile.email = payload.email;
      }
    },
    setUserProfile: (state, payload) => {
      let profile = {
        username: null, 
        email: null, 
        bio: null, 
        image: null, 
        addressInvoice: null, 
        addressesDelivery: [], 
        settings: {}
      };
      if ( payload.username ) { profile.username = payload.username; }
      if ( payload.email ) { profile.email = payload.email; }
      if ( payload.bio ) { profile.bio = payload.bio; }
      if ( payload.addresses && payload.addresses.length>0 ) { 
        let invoiceAddressSet = false;
        payload.addresses.forEach(address => {
          if ( address.type && address.type==="invoice" && !invoiceAddressSet ) {
            profile.addressInvoice = address;
          } else if ( address.type && address.type==="delivery" ) {
            profile.addressesDelivery.push(address);
          }
        });
      }
      if ( payload.bio ) { profile.bio = payload.bio; }
      if ( payload.settings ) { profile.settings = payload.settings; }

      // update only those params that are not set to null (can be empty string)
      state.user.profile = {};
      Object.keys(profile).forEach(function(key){
        if ( profile[key] && profile[key]!==null ) {
          state.user.profile[key] = profile[key];
        }
      });
    },
    setUserProfileImage: (state, payload) => {
      let url = "https://stretchshop.app/static";

      if ( state.coredata && state.coredata.settings && state.coredata.settings.assets && state.coredata.settings.assets.url ) {
        url = state.coredata.settings.assets.url
      }
      if ( payload ) {
        if ( state.user && state.user.profile && state.user.profile.image ) {
          state.user.profile.image = payload;
        }
        state.user.avatar = url + "/users/profile/" + payload +"?"+ (new Date()).getTime();
      }
    }, 
    setCoreData: (state, payload) => {
      state.language = payload.language;
      state.coredata = payload.coredata;
    }, 
    setOrder: (state, payload) => {
      state.order = payload;
    },
    addNewDeliveryAddress: (state, payload) => {
      state.user.profile.addressesDelivery.unshift(payload);
    },
    removeDeliveryAddress: (state, payload) => {
      state.user.profile.addressesDelivery.splice(payload,1);
    },
    setLanguage: (state, payload) => {
      state.language = payload;
    },
    setCurrency: (state, payload) => {
      state.currency = payload;
    },
    setTranslates: (state, payload) => {
      state.translates = payload;
    }
  },
  actions: {
    setCart: (context, payload) => {
      console.log('setting cart');
      if (payload && payload!=null) {
        payload.priceTotal = 0;
        let itemsCount = 0;
        let itemsAmountCount = 0;
        let cartPriceTotal = 0;
        let cartItemTypology = { types: [], subtypes: [] };
        if (payload.items && payload.items.length>0) {
          payload.items.forEach(element => {
            // count priceTotal for cart
            itemsCount++;
            itemsAmountCount = itemsAmountCount + element.amount;
            cartPriceTotal += (element.price * element.amount)
            // set variables for delivery & payment types
            if ( cartItemTypology.types.indexOf(element.type)<0 ) {
              cartItemTypology.types.push(element.type);
            }
            if ( cartItemTypology.subtypes.indexOf(element.subtype)<0 ) {
              cartItemTypology.subtypes.push(element.subtype);
            }
          });
        }
        payload.itemsCount = itemsCount;
        payload.itemsAmountCount = itemsAmountCount;
        payload.priceTotal = cartPriceTotal;
        payload.cartItemTypology = cartItemTypology;
        context.commit('setCart', payload);
      }
    },
    setCoreData: (context, payload) => {
      if (payload && payload!=null) {
        let coredata = JSON.parse(JSON.stringify(payload));
        payload.coredata = coredata;
        if ( payload.coredata.lang ) {
          payload.language = payload.coredata.lang;
        }
        context.commit('setCoreData', payload);
      }
    },
    setUser: ({commit, state}, payload) => {
      if (payload && payload!=null) {
        commit('setUserProfile', payload);
        if ( payload.image ) { 
          commit('setUserProfileImage', payload.image);
        }
      }
    },
    setUserProfileImage: (context, payload) => {
      if (payload && payload!=null) {
        context.commit('setUserProfileImage', payload);
      }
    },
    setUserInvoiceAddress: (context, payload) => {
      if (payload && payload!=null) {
        context.commit('setUserInvoiceAddress', payload);
      }
    },
    setOrder: ({commit, state}, payload) => {
      if (payload && payload!=null) {

        let delivery = {};
        if (payload.order && payload.order.settings && payload.order.settings.deliveryMethods && payload.order.settings.deliveryMethods.length>0) {
          // console.log(' --- in --- ');
          payload.order.settings.deliveryMethods.forEach(element => {
            if ( !delivery[element.type] ) {
              // console.log('element', element);
              delivery[element.type] = [];
            }
            delivery[element.type].push( element );
          });
          payload.order.settings.deliveryMethodsTypes = delivery;
        }

        commit('setOrder', payload);
      }
    },
    updateUser: function({commit, state}, payload) {
      if (payload && payload!=null) {
        // send payload to server
        if ( state.user.token && state.user.token.toString().trim()!=='' && !this._vm.$httpc.defaults.headers.common['Authorization'] ) {
          this._vm.$httpc.defaults.headers.common['Authorization'] = "Token "+state.user.token; // TODO - move to httpc
        }
        console.log( state.user.token);
        console.log( this._vm.$httpc.defaults.headers );     
        this._vm.$httpc.put("user", { user: payload })
        .then(function (response) {
          if (response.data.user) {
            commit('setUserProfile', response.data.user);
          }
        });
      }
    },
    addNewDeliveryAddress: (context, payload) => {
      if (payload && payload!=null) {
        context.commit('addNewDeliveryAddress', payload);
      }
    },
    removeDeliveryAddress: (context, payload) => {
      if (typeof payload !=='undefined' && payload>-1) {
        context.commit('removeDeliveryAddress', payload);
      }
    },
    checkLanguage: ({commit, state}, payload) => {
      if (typeof payload !=='undefined' && payload.toString().trim()!=='') {
        //state.language
        let isValidLanguage = false;
        let foundLanguage = null;
        if ( state.coredata && state.coredata.langs && state.coredata.langs.length>0 ) {
          for (let i=0; i<state.coredata.langs.length; i++) {
            if (state.coredata.langs[i].code == payload) {
              isValidLanguage = true;
              foundLanguage = state.coredata.langs[i];
              break;
            }
          }
        }
        return foundLanguage;
      }
      return null;
    },
    setLanguage: ({commit, state}, payload) => {
      if (typeof payload !=='undefined' && payload.toString().trim()!=='') {
        //state.language
        let isValidLanguage = false;
        let foundLanguage = null;
        if ( state.coredata && state.coredata.langs && state.coredata.langs.length>0 ) {
          for (let i=0; i<state.coredata.langs.length; i++) {
            if (state.coredata.langs[i].code == payload) {
              isValidLanguage = true;
              foundLanguage = state.coredata.langs[i];
              break;
            }
          }
        }
        if (isValidLanguage) {
          commit('setLanguage', foundLanguage);
        }
      }
    },
    setCurrency: ({commit, state}, payload) => {
      if (typeof payload !=='undefined' && payload.toString().trim()!=='') {
        //state.currency
        let isValidCurrency = false;
        let foundCurrency = null;
        if ( state.coredata && state.coredata.currencies && state.coredata.currencies.length>0 ) {
          for (let i=0; i<state.coredata.currencies.length; i++) {
            if (state.coredata.currencies[i].code == payload) {
              isValidCurrency = true;
              foundCurrency = state.coredata.currencies[i];
              break;
            }
          }
        }
        if (isValidCurrency) {
          commit('setCurrency', foundCurrency);
        }
      }
    },
    setTranslates: (context, payload) => {
      if (payload && payload!=null) {
        context.commit('setTranslates', payload);
      }
    },
  }
});


