<template>
  <div class="orderDeliveryPayment">
    <h2 class="title is-2" translate="yes">Delivery Payment</h2>
    <form v-on:submit.prevent="updateDeliveryPaymentData">

      <div>
        <h3 class="title is-3 has-text-left" translate="yes">Delivery</h3>
        <div class="deliveryType" v-if="$store.state.order.order.settings.deliveryMethodsTypes && Object.keys($store.state.order.order.settings.deliveryMethodsTypes).length>0">
          <div class="control" v-for="(type, t) in Object.keys($store.state.order.order.settings.deliveryMethodsTypes)" :key="t">
            <div v-if="($store.state.cart && $store.state.cart.cartItemTypology && $store.state.cart.cartItemTypology.subtypes.indexOf(type)>-1)">
              <h4 class="title is-4 has-text-left"><span>{{ type }}</span> <span translate="yes">goods</span></h4>
              <div v-for="(delivery, d) in $store.state.order.order.settings.deliveryMethodsTypes[type]" :key="d">
                <label class="radio-button">
                  <input type="radio" :name="'type-'+type"
                    @click="setDelivery(type,delivery.codename)"
                    :value="delivery.codename" 
                    :checked="$parent.orderParams.order.data.deliveryData && $parent.orderParams.order.data.deliveryData.codename[type] && $parent.orderParams.order.data.deliveryData.codename[type]==delivery.codename" />
                  {{ delivery.name[$store.state.language.code] }} ({{ delivery.prices[0].price }} {{ $store.state.currency.symbol }})
                </label>
              </div>
            </div>
          </div>
        </div>
      </div>

      <div class="deliveryAddressBlock" v-if="($store.state.cart && $store.state.cart.cartItemTypology && $store.state.cart.cartItemTypology.subtypes.indexOf('physical')>-1)">
        <label class="blockLabel"><span translate="yes">Use custom delivery address</span> &nbsp;<input type="checkbox" class="checkbox" v-model="showDeliveryAddress" /></label>
        <div class="deliveryAddressWrapper is-clearfix" v-if="showDeliveryAddress">
          <div class="columns is-variable is-2" v-if="$store.state.user.profile.addressesDelivery && $store.state.user.profile.addressesDelivery.length>0">
            <div class="column is-12">
              <div class="field for-select">
                <label class="label" translate="yes">Previously used addresses:</label>
                <select class="select" @change="fillWithSelectedDeliveryAddress">
                  <option value="" translate="yes">Please select</option>
                  <option v-for="(deliAdd, d) in $store.state.user.profile.addressesDelivery" :key="d" :value="d">
                    {{deliAdd.street}}, {{deliAdd.zip}}
                  </option>
                </select>
              </div>
            </div>
          </div>

          <div class="columns is-variable is-2">
            <div class="column is-6">
              <div class="field">
                <label class="label" translate="yes">First Name</label>
                <div class="control">
                  <input class="input" type="text" v-model="deliveryAddress.nameFirst" translate="yes" placeholder="First Name">
                </div>
              </div>
            </div>
            <div class="column is-6">
              <div class="field">
                <label class="label" translate="yes">Last Name</label>
                <div class="control">
                  <input class="input" type="text" v-model="deliveryAddress.nameLast" translate="yes" placeholder="Last Name">
                </div>
              </div>
            </div>
          </div>

          <div class="columns is-variable is-2">
            <div class="column is-12">
              <div class="field">
                <label class="label" translate="yes">Street</label>
                <div class="control">
                  <input class="input" type="text" v-model="deliveryAddress.street" translate="yes" placeholder="Street">
                </div>
              </div>
            </div>
          </div>

          <div class="columns is-variable is-2">
            <div class="column is-12">
              <div class="field">
                <label class="label" translate="yes">Street 2</label>
                <div class="control">
                  <input class="input" type="text" v-model="deliveryAddress.street2" translate="yes" placeholder="Street 2">
                </div>
              </div>
            </div>
          </div>

          <div class="columns is-variable is-2">
            <div class="column is-4">
              <div class="field">
                <label class="label" translate="yes">ZIP</label>
                <div class="control">
                  <input class="input" type="text" v-model="deliveryAddress.zip" translate="yes" placeholder="ZIP">
                </div>
              </div>
            </div>
            <div class="column is-8">
              <div class="field">
                <label class="label" translate="yes">City</label>
                <div class="control">
                  <input class="input" type="text" v-model="deliveryAddress.city" translate="yes" placeholder="City">
                </div>
              </div>
            </div>
          </div>

          <div class="columns is-variable is-2">
            <div class="column is-6">
              <div class="field">
                <label class="label" translate="yes">State</label>
                <div class="control">
                  <input class="input" type="text" v-model="deliveryAddress.state" translate="yes" placeholder="State">
                </div>
              </div>
            </div>
            <div class="column is-6">
              <div class="field">
                <label class="label" translate="yes">Country</label>
                <div class="control">
                  <input class="input" type="text" v-model="deliveryAddress.country" translate="yes" placeholder="Country">
                </div>
              </div>
            </div>
          </div>

          <div class="columns is-variable is-1 is-centered">
            <div class="column is-6">
              <div class="field">
                <label class="label" translate="yes">Phone</label>
                <div class="control is-clearfix">
                  <input class="input" type="text" v-model="deliveryAddress.phone" translate="yes" placeholder="Phone">
                </div>
              </div>
            </div>
          </div>
        </div>
      </div>

      <div>
        <h3 class="title is-3 has-text-left" translate="yes">Payment</h3>
        <div class="paymentType" v-if="$store.state.order.order.settings.paymentMethods && Object.keys($store.state.order.order.settings.paymentMethods).length>0">
          <div class="control" v-for="(payment, p) in $store.state.order.order.settings.paymentMethods" :key="p">
            <div :class="'payment-'+payment.codename" v-if="($store.state.cart && $store.state.cart.cartItemTypology && ($store.state.cart.cartItemTypology.types.indexOf(payment.type)>-1 || typeof payment.type === 'undefined') )">
              <label class="radio">
                <input type="radio" name="paymentType" v-model="paymentData" :value="payment.codename" />
                {{ payment.name[$store.state.language.code] }}
              </label>
            </div>
          </div>
        </div>
      </div>

      <div class="subscriptionBlock" v-if="($store.state.cart && $store.state.cart.cartItemTypology && $store.state.cart.cartItemTypology.types.indexOf('subscription')>-1)">
        <label><span translate="yes">Renew my subscription</span> &nbsp;<input type="checkbox" class="checkbox" v-model="renewSubscription" /></label>
      </div>

      <button class="button is-success">
        <font-awesome-icon icon="check" /> &nbsp; 
        <span translate="yes">Set delivery and payment</span>
      </button>
    </form>
  </div>
</template>

<script>
export default {
  name: "OrderDeliveryPayment",
  data () {
    return {
      deliveryData: { physical: null, digital: null }, // change to accepty multiple types eg. physical & digital
      paymentData: null, // change to accepty multiple types eg. once & regulary
      showDeliveryAddress: false,
      deliveryAddress: {
        "type":"delivery",
        "nameFirst":"",
        "nameLast":"",
        "street":"",
        "street2":"",
        "zip":"",
        "city":"",
        "state":"",
        "country":"",
        "phone":""
      }
    }
  },
  computed: {
  },
  created: function(){
    if ( this.$parent && this.$parent.orderParams ) {
      if ( this.$parent.orderParams.order.data ) {
        //console.log('this.$parent.orderParams.order.data.deliveryData.codename: ', this.$parent.orderParams.order.data.deliveryData.codename);
        // console.log('this.$parent.orderParams.order.data.deliveryData.codename[physical]: ', this.$parent.orderParams.order.data.deliveryData.codename[physical].value);
        if ( this.$parent.orderParams.order.data.deliveryData && this.$parent.orderParams.order.data.deliveryData.codename ) {
          this.deliveryData = this.$parent.orderParams.order.data.deliveryData.codename;
        }
        if ( this.$parent.orderParams.order.data.paymentData && this.$parent.orderParams.order.data.paymentData.codename ) {
          this.paymentData = this.$parent.orderParams.order.data.paymentData.codename;
        }
      }
      if ( this.$parent.orderParams.order.addresses && this.$parent.orderParams.order.addresses.deliveryAddress ) {
        this.deliveryAddress = this.$parent.orderParams.order.addresses.deliveryAddress;
      }
    }
  },
  methods: {
    updateDeliveryPaymentData: function() {
      if ( this.showDeliveryAddress ) {
        if ( !this.$parent.orderParams.order.addresses ) {
          this.$parent.orderParams.order.addresses = {};
        }
        if ( !this.$parent.orderParams.order.addresses.deliveryAddress ) {
          this.$parent.orderParams.order.addresses.deliveryAddress = [];
        }
        this.$parent.orderParams.order.addresses.deliveryAddress = this.deliveryAddress;
      }
      // set order data if none
      if ( !this.$parent.orderParams.order.data ) {
        this.$parent.orderParams.order.data = {};
      }
      // set delivery data
      if ( this.deliveryData && this.deliveryData!=null ) { 
        if ( !this.$parent.orderParams.order.data.deliveryData ) {
          this.$parent.orderParams.order.data.deliveryData = {
            codename: null
          };
        }
        this.$parent.orderParams.order.data.deliveryData.codename = this.deliveryData;
      }
      // set payment data
      if ( this.paymentData && this.paymentData!=null ) {
        if ( !this.$parent.orderParams.order.data.paymentData ) {
          this.$parent.orderParams.order.data.paymentData = {
            codename: null
          };
        }
        this.$parent.orderParams.order.data.paymentData.codename = this.paymentData;
      }
      // update order
      this.$parent.updateOrder();
    }, 
    checkIfDeliveryAddressExists: () => {
      if ( !this.$parent.orderParams.order.addresses.deliveryAddress && this.$parent.orderParams.order.addresses.deliveryAddress.length>0 ) {
        let jsonNewAddress = JSON.stringify(deliveryAddress);
        this.$parent.orderParams.order.addresses.deliveryAddress.forEach(address => {
          let jsonAddress = JSON.stringify(address);
          if ( jsonAddress == jsonNewAddress ) {
            return true;
          }
        });
      }
      return false;
    },
    fillWithSelectedDeliveryAddress (event) {
      if ( event.target.value && event.target.value!="" && event.target.value>-1 ) {
        this.deliveryAddress = this.$store.state.user.profile.addressesDelivery[event.target.value];
      }
    },
    showDeliveryTypeByCartItems (deliveryCodename, deliveryTypeName) {
      // use 
      // if digital in cart, set this.showVars.deliveryTypeDigital=true (false if not) to show download (= delivery types for digital and without type)
      // if physical in cart, set this.showVars.deliveryTypePhysical=true (false if not) to show courier and branch (= delivery types for physical and without type)
    },
    showPaymentTypeByCartItems (paymentCodename, paymentTypeName) {
      // if subscription set this.showVars.paymetTypeSubscription=true (false if not) to show eg. checkbox 
    },
    setDelivery (deliveryType, deliveryValue) {
      console.log(deliveryType, deliveryValue, this.deliveryData);
      // console.log("this.$parent.orderParams.order.data.deliveryData.codename["+deliveryType+"]", this.$parent.orderParams.order.data.deliveryData.codename[deliveryType]);
      // console.log(this.$parent.orderParams.order.data.deliveryData.codename[deliveryType]+' == '+deliveryValue);
      // console.log("this.$store.state.order.order.data.deliveryData.codename["+deliveryType+"].value", this.$store.state.order.order.data.deliveryData.codename[deliveryType].value);
      // console.log(this.$store.state.order.order.data.deliveryData.codename[deliveryType].value+' == '+deliveryValue);
      this.deliveryData[deliveryType] = deliveryValue;
    }
  }
}
</script>

