import Homepage from './components/Homepage.vue';
import UsersLogin from './components/users/Login.vue';
import UsersRegister from './components/users/Register.vue';
import UsersMe from './components/users/Me.vue';
import UsersVerify from './components/users/Verify.vue';
import UsersReset from './components/users/Reset.vue';
import CategoryList from './components/products/CategoryList.vue';
import ProductDetail from './components/products/ProductDetail.vue';
import OrderCart from './components/orders/Cart.vue';

import { store } from './store/store';

const basePathRedir = "/"+store.state.language.code;
const basePath = "/:lang"; // "/"+store.state.language.code;

export const routes = [
    { path: "/", redirect: basePathRedir }, 
    { path: basePath+ "/", name: "homepage", component: Homepage }, 
    { path: basePath+ "/login", name: "login", component: UsersLogin }, 
    { path: basePath+ "/register", name: "register", component: UsersRegister }, 
    { path: basePath+ "/user", name: "me", component: UsersMe }, 
    { path: basePath+ "/user/reset", name: "reset", component: UsersReset }, 
    { path: basePath+ "/user/verify/:id/:hash", name: "verify", component: UsersVerify }, 
    { path: basePath+ "/products", name: "productsHome", component: CategoryList },
    { path: basePath+ "/services", name: "servicesHome", component: CategoryList },
    { path: basePath+ "/category/:category", name: "categoryList", component: CategoryList },
    { path: basePath+ "/category/:category/product/:product", name: "productDetail", component: ProductDetail }, 
    { path: basePath+ "/cart", name: "cart", component: OrderCart }
];