<template>
  <div id="summary">
    <h2 class="title is-2" translate="yes">Summary</h2>
    <form v-on:submit.prevent="submitOrder">
      <div class="columns is-variable is-2">
        <div class="column is-12" translate="yes">
          This would be your order:
        </div>
      </div>

      <div class="columns is-variable is-2">
        <div class="column is-12">
          <label class="checkbox">
            <input type="checkbox" @click="setConfirmationDate($event)">
            <span translate="yes">I agree to the</span> <a href="#" translate="yes">terms and conditions</a>
          </label>
        </div>
      </div>

      <button class="button is-success">
        <font-awesome-icon icon="check" /> &nbsp; 
        Submit order
      </button>
    </form>
  </div>
</template>

<script>
export default {
  name: "OrderDeliveryPayment",
  data () {
    return {
			userConfirmation: null
    }
  },
  methods: {
    submitOrder: function() {
      this.$parent.orderParams.order.dates.userConfirmation = this.userConfirmation;
      this.$parent.updateOrder();
    },
    setConfirmationDate: function(event) {
      if (event.target.checked) {
        let nowTime = new Date();
        this.userConfirmation = nowTime.getTime(); //on checkbox check click;
      }
    }
  }
}
</script>

