<template>
  <div id="app">
    <app-header></app-header>
    <main>
      <router-view :key="$route.fullPath"></router-view>
    </main>
    <footer class="footer"></footer>
  </div>
</template>

<script>
import Header from "./components/global/Header.vue";

export default {
  name: 'app',
  components: {
    "app-header": Header
  },
  created: function () {
  },
  watch:{
    $route (to, from){
      // everytime route changes, translate content
      if ( typeof translateContent !== 'undefined' && this.$store.state.language.code==this.$store.state.coredata.lang.code ) {
        translateContent(this.$store.getters.getTranslates);
      }
    }
  },
  events: {
    changeRouterLanguageEvent: function(newlanguage) {
      return this.changeRouterLanguage(newLanguage);
    }, 
    translateContentEvent: function() {
      return this.translateContent(this.$store.getters.getTranslates);
    }
  },
  methods: {
    changeRouterLanguage: function(newLanguage) {
      if ( this.$router.options && this.$router.options.routes && this.$router.options.routes.length>0 ) {
        for (let i = 0; i < this.$router.options.routes.length; i++) {
          if ( this.$router.options.routes[i].path=='/' ) {
            this.$router.options.routes[i].redirect = "/"+newLanguage;
          } else {
            let pathArray = this.$router.options.routes[i].path.split("/");
            if ( pathArray!=null && pathArray.length>1 ) {
              pathArray[1] = newLanguage;
              this.$router.options.routes[i].path = pathArray.join("/");
            }
          }
        }
      }
    },

    translateContent: function(translatesArray) {
      // if translates available and language is not the original one
      let activeLang = this.$store.getters.getLanguage;
      let defaultLang = '';
      if ( this.$store.state.coredata && this.$store.state.coredata.lang && this.$store.state.coredata.lang.code ) {
        defaultLang = this.$store.state.coredata.lang.code;
      }
      if ( 
        translatesArray && translatesArray!=null && translatesArray && 
        ((translatesArray[activeLang] && translatesArray[activeLang].length>0) || activeLang==defaultLang) 
        ) {
        // translation for case language is changed back to original or to language that is not loaded
        let isOriginal = false;
        if (activeLang!=null && activeLang==defaultLang) {
          activeLang = Object.keys(translatesArray)[0];
          isOriginal = true;
        }
        console.log( 'translateContent 3', isOriginal, translatesArray );
        for (let i=0; i<translatesArray[activeLang].length; i++) {
          if (translatesArray[activeLang][i].type==="text") {
            let selector = translatesArray[activeLang][i].selector.replace("> router-link.","> a.").replace("> template > ","");
            let element = this.$el.querySelector(selector);
            if (element && element!=null) {
              console.log( (element!=null), translatesArray[activeLang][i].original +"-->"+ translatesArray[activeLang][i].string , " || selector: " + selector );
              if (isOriginal) {
                element.textContent = translatesArray[activeLang][i].original;
              } else {
                element.textContent = translatesArray[activeLang][i].string;
              }
            } else {
              console.log( (element!=null), translatesArray[activeLang][i].original , " || selector: " + selector );
            }
          }
        } // END for loop of translatesArray
      } // END if translatesArray
    }
  }
}
</script>

<style lang="scss">
#app {
  text-align: center;
  color: #2c3e50;
}

