<template>
  <header class="header">
    <nav class="navbar is-fixed-top" role="navigation" aria-label="main navigation">
      <div class="container">

      <div class="navbar-brand">
        <router-link to="/" tag="a" class="navbar-item" active-class="is-active" exact>
          <img src="/assets/_site/logo-small-160.png" class="logo">
        </router-link>

        <a role="button" class="navbar-burger burger" aria-label="menu" aria-expanded="false" data-target="navbarBasicExample">
          <span aria-hidden="true"></span>
          <span aria-hidden="true"></span>
          <span aria-hidden="true"></span>
        </a>
      </div>


      <div id="navbarBasicExample" class="navbar-menu">
        <div class="navbar-start">
          <router-link to="/" tag="a" class="navbar-item" active-class="is-active" exact translate="yes">Home</router-link>

          <a class="navbar-item" translate="yes">
            Info
          </a>

          <router-link v-bind:to="{name: 'productsHome'}" tag="a" class="navbar-item" active-class="is-active" exact translate="yes">Products</router-link>

          <router-link v-bind:to="{name: 'servicesHome'}" tag="a" class="navbar-item" active-class="is-active" exact translate="yes">Services</router-link>

          <a class="navbar-item" translate="yes">
            Articles
          </a>
        </div>


        <div class="navbar-end">
            <div class="navbar-item has-dropdown is-hoverable">
              <router-link 
                v-if="$store.state.language" 
                to="#"
                tag="a" 
                class="navbar-link" active-class="is-active">
                {{ ($store.state.language && $store.state.language.code) ? $store.state.language.code : '' }}
                /
                {{ ($store.state.currency && $store.state.currency.symbol) ? $store.state.currency.symbol : '' }}
              </router-link>
              <div class="navbar-dropdown" v-if="$store.state.coredata && $store.state.coredata.langs">
                <a v-for="(lang, i) in $store.state.coredata.langs" class="navbar-item" :key="i+'langs'" @click.stop.prevent="setLang(lang.code)">
                  {{lang.name}}
                </a>
                <hr class="navbar-divider">
                <a v-for="(currency, j) in $store.state.coredata.currencies" class="navbar-item" :key="j+'-currecies'" @click.stop.prevent="setCurrency(currency.code)">
                  {{currency.code}}
                </a>
              </div>
            </div>

            <div class="navbar-item userCart" v-bind:class="{ 'has-dropdown': hasCart, 'is-hoverable': hasCart }">
              <router-link 
                v-if="$store.state.cart!=null" 
                v-bind:to="{name:'cart'}" 
                tag="a" 
                class="navbar-link userCartLink" 
                active-class="is-active">
                  <span translate="yes">Cart</span>
                  <span v-if="$store.state.cart && $store.state.cart.itemsAmountCount && $store.state.cart.itemsAmountCount>0" class="cartItemsCount">
                    {{ $store.state.cart.itemsAmountCount }}
                  </span>
                  <span v-else class="cartItemsCount">
                    0
                  </span>
                  <div v-if="$store.state.cart!=null" class="navbar-dropdown">
                    <div class="navbar-item">
                      <table class="table">
                        <tbody>
                          <tr v-for="(item, k) in $store.state.cart.items" :key="k+'-cart'">
                            <th>{{ item.name[$store.state.language.code] }}</th>
                            <td>x{{ item.amount }}</td>
                            <td>{{ $store.state.numeral(item.price).format('0 0.00') }}</td>
                            <td><a @click.stop.prevent="clearCart(item._id)"><font-awesome-icon icon="trash" /></a></td>
                          </tr>
                        </tbody>
                      </table>
                    </div>
                    <div v-if="$store.state.cart && $store.state.cart.priceTotal!=null">
                      <span translate="yes">Total price</span> {{ $store.state.numeral($store.state.cart.priceTotal).format('0 0.00') }}
                    </div>
                    <hr class="navbar-divider">
                    <router-link 
                      v-if="$store.state.cart!=null" 
                      v-bind:to="{name:'cart'}" 
                      tag="a" 
                      class="button is-success" 
                      active-class="is-active" translate="yes">Go to Cart</router-link>
                  </div>
              </router-link>
            </div>
            <div class="navbar-item has-dropdown is-hoverable">
              <router-link 
                v-if="$store.state.user.logged" 
                v-bind:to="{name:'me'}" 
                tag="a" 
                class="navbar-link" active-class="is-active">
                <div class="user--avatar-wrapper" :style="{ 'background-image': 'url('+userProfileImage+')' }"></div>
                {{ ($store.state.user && $store.state.user.profile && $store.state.user.profile!=null && $store.state.user.profile.username.toString().trim()!='') ? $store.state.user.profile.username : "Profile" }}
              </router-link>
              <div class="navbar-dropdown">
                <a class="navbar-item">
                  <span translate="yes">Orders</span>
                </a>
                <a class="navbar-item">
                  <span translate="yes">Articles</span>
                </a>
              </div>
            </div>

            <div class="buttons">
              <router-link 
                v-if="!$store.state.user.logged || $store.state.user.logged!=true" 
                v-bind:to="{name:'register'}" 
                tag="a" 
                class="navbar-item button is-primary" 
                active-class="is-active">
                  <strong translate="yes">Sign up</strong>
              </router-link>
              <router-link 
                v-if="!$store.state.user.logged || $store.state.user.logged!=true" 
                v-bind:to="{name:'login'}" 
                tag="a" 
                class="navbar-item button is-light" 
                active-class="is-active">
                  <span translate="yes">Log in</span>
              </router-link>
              <a v-if="$store.state.user.logged && $store.state.user.logged==true" 
                v-on:click="userLogout" class="button is-light" translate="yes">Logout</a>
            </div>
          
        </div>
      </div>

      </div>
    </nav>
  </header>
</template>

<script>
export default {
  name: 'appHeader',
  data() {
    return {
      userLogged: false,
      cartItemsCount: 0
    }
  },
  computed: {
    userProfileImage: function() {
      return this.$store.getters.getProfileImage();
    }
  },
  mounted: function () {
    this.getCart();
  },
  methods: {
    userLogout() {
      this.$store.state.user.logged = false;
      this.$store.state.user.token = null;
      this.$store.state.user.profile = null;
      this.$httpc.defaults.headers.common['Authorization'] = null;
      localStorage.removeItem("token");
      sessionStorage.removeItem("token");

      this.$httpc.get("users/logout").then(result => {
        this.$router.push({"name": "login"});
      });
    }, 
    getCart() {
      // this.$store.getters.getCart
      let self = this;
      this.$httpc.get("cart")
      .then( (response) => {
        // console.log(response.data);
        if (response.data) {
          self.$store.dispatch('setCart',response.data);
        }
      })
      .catch(function (error) {
        // handle error
        console.log(error);
        return null;
      })
      .then(function () {
        // always executed
        return null;
      });
    },
    hasCart() {
      return ( this.$store.state.cart && this.$store.state.cart!=null && this.$store.state.cart.items && this.$store.state.cart.items.length>0 );
    },
    clearCart(itemId) {
      let self = this;
      itemId = (typeof itemId !== 'undefined') ?  itemId : null;
      let itemUrl = "";
      if ( itemId!=null && itemId.toString().trim()!='' ) {
        itemUrl = "/"+itemId;
      }
      this.$httpc.delete("cart"+itemUrl)
      .then( (response) => {
        console.log(response.data);
        if (response.data) {
          self.$store.dispatch('setCart',response.data);
        }
      })
      .catch(function (error) {
        // handle error
        console.log(error);
        return null;
      })
      .then(function () {
        // always executed
        return null;
      });
    },
    setLang: function(langCode) {
      let self = this;
      /**
       * load new translation if:
       *  - new lang does not exist in translates,
       *  - new lang is not same as default lang and 
       * */
      if (
        this.$store.state.coredata && this.$store.state.coredata.lang && this.$store.state.coredata.lang.code && // default language exists
        (
          !this.$store.state.translates || // no translates
          ( // or translates exist, but not for this language
            this.$store.state.translates && typeof this.$store.state.translates[langCode] == 'undefined' || 
            this.$store.state.translates[langCode].length<1
          )
        ) && 
        langCode!=this.$store.state.coredata.lang.code // active language is not same as default language
      ) {
        this.$httpc.get("/coredata/translation?lang="+langCode)
        .then((response) => {
          if (response.data) {
            self.$store.dispatch("setTranslates", response.data);
            // save to store
            self.$store.dispatch('setLanguage',langCode);
            // save to localStorage
            localStorage.setItem('language', langCode);
            // translate
            self.$parent.translateContent(this.$store.getters.getTranslates);
          }
        });
      } else { // no need to load new translation
        // save to store
        this.$store.dispatch('setLanguage',langCode);
        // save to localStorage
        localStorage.setItem('language', langCode);
        // translate
        this.$parent.translateContent(this.$store.getters.getTranslates);
      }
    },
    setCurrency: function(currencyCode) {
      this.$store.dispatch('setCurrency',currencyCode);
    }
  }
}
</script>


