import Vue from 'vue'
import VueRouter from 'vue-router';
import Buefy from 'buefy';
import 'buefy/dist/buefy.css';
import App from './App.vue';

import { store } from './store/store';
import { routes } from './routes';
import { httpc } from "./http-common";

import { library, dom } from '@fortawesome/fontawesome-svg-core';
import { faTrash, faCartPlus, faExclamationCircle, faCheck, faExclamationTriangle, faFileUpload, faSearch, faChevronLeft, faChevronRight, faAngleLeft, faAngleRight } from '@fortawesome/free-solid-svg-icons';
import { FontAwesomeIcon } from '@fortawesome/vue-fontawesome';
library.add(faTrash, faCartPlus, faExclamationCircle, faCheck, faExclamationTriangle, faFileUpload, faSearch, faCheck, faAngleLeft, faAngleRight);
dom.watch(); // this will kick off the initial replacement of i to svg tags and configure a MutationObserver
Vue.component('font-awesome-icon', FontAwesomeIcon);
Vue.config.productionTip = false;

Vue.use(VueRouter);
Vue.use(Buefy, {
  defaultIconPack: "fa"
});

import { helpers } from "./helpers/global";
const helpersPlugin = {
  install () {
      Vue.helpers = helpers
      Vue.prototype.$helpers = helpers
  }
}
Vue.use(helpersPlugin);

Vue.config.productionTip = false;
Vue.prototype.$httpc = httpc;


const router = new VueRouter({
  mode: 'history',
  routes
});

const vueAppInstance = new Vue({
  render: h => h(App),
  router,
  store,
  data() {
    return {
      loadingIsShowing: false,
      loadingIsFullPage: true,
      loadingComponent: null
    }
  },
  created: function() {
    // in mounted event vm.$el is available
    let self = this;
    /**
     * manage USER:
     * 1. check if token is set
     * 2. verify token and if user is logged
     * */
    // if we have token set, use it for login
    let token = null;
    let remember = false;
    let translates = null;
    token = localStorage.getItem('token');
    if ( token==null ) {
      token = sessionStorage.getItem('token');
    } else {
      remember = true;
    }
    if (token!=null) {
      this.$httpc.defaults.headers.common['Authorization'] = "Token "+token;
    }
    this.$httpc.get("/coredata?transLang="+this.$store.getters.getLanguage)
    .then((response) => {
      if (response.data) {
        let tempUser = response.data.user;
        // delete user & translate to get pure coredata
        delete response.data.user;
        translates = response.data.translation;
        delete response.data.translation;
        self.$store.dispatch('setCoreData',response.data);
        // when we have core data, set user
        if (tempUser) {
          self.$store.dispatch("setUser", tempUser);
          // if token, get user profile
          if ( token && token!==null && token.toString().trim()!=='' ) {
            self.$store.state.user.logged = true;
            self.$store.state.user.token = token;
            self.$store.state.user.remember = remember;
          } else {
            self.$store.state.user.logged = false;
            self.$store.state.user.token = null;
            self.$store.state.user.remember = false;
          }
        }

        if (translates && translates!=null) {
          self.$store.dispatch("setTranslates", translates);
        }
      }
    })
    .catch(function (error) {
      // handle error
      console.log(error);
    })
    .then(function () {
      self.manageLanguageFirstTime();
      // wait until all content is rendered and then fire translationEvent
      self.$nextTick(function () {
        // Code that will run only after the
        // entire view has been rendered
        // translate page
        this.$children[0].translateContent(self.$store.getters.getTranslates);
        // this.$children[0].changeRouterLanguage(language);
      })
    });
  }, // END mounted event
  methods: {
    manageLanguageFirstTime() {
      let self = this;
      // always executed
      /** 
       * manage LANGUAGE - from most default to most specific values:
       * 1. get default language
       * 2. if possible, overwrite by language from URL
       * 3. if possible, check prefered language from browser
       * 4. if possible, overwrite by language from localStorage
       * 5. if possible, overwrite by language from store.state.user.settings.language
       * 6. set result language in store, localStorage and if possible save it in user profile
       * 7. rebuild routes language and redirect
       * */
      // language from default
      let language = self.$store.state.language.code;
      // language from url
      let urlArray = window.location.pathname.split("/");
      let urlLanguage = language;
      if (urlArray && urlArray.length>1) {
        language = urlArray[1];
        urlLanguage = urlArray[1];
      }
      // language from browser
      if ( (window.navigator.languages && window.navigator.languages.length>0) || 
          (window.navigator.language) || (window.navigator.userLanguage) 
      ) {
        let browserLang = window.navigator.language;
        if (window.navigator.languages && window.navigator.languages.length>0) {
          let browserLangArray = window.navigator.languages[0].split('-');
          if (browserLangArray.length>0) {
            browserLang = browserLangArray[0];
          }
        } else if (window.navigator.userLanguage) {
          browserLang = window.navigator.userLanguage;
        }
        language = browserLang;
      }
      // language from localStorage
      let localStorageLang = localStorage.getItem('language');
      if ( localStorageLang && self.$store && self.$store.state.coredata && self.$store.state.coredata!=null ) {
        let langValid = self.$helpers.checkIfObjectInArray(localStorageLang, self.$store.state.coredata.langs);
        if ( langValid!=null ) {
          language = localStorageLang;
        }
      }
      // language from store.state.user.settings.language
      if ( self.$store.state.user && self.$store.state.user.profile && 
        self.$store.state.user.profile.settings && self.$store.state.user.profile.settings.language 
      ) {
        language = self.$store.state.user.profile.settings.language;
      }
      // set language to store
      self.$store.dispatch("setLanguage", language);
      // set language to localStorage
      localStorage.setItem('language', language);
    }, 

    loadingShow() {
      this.loadingComponent = this.$loading.open({
          container: this.loadingIsFullPage ? null : this.$refs.element.$el
      });
    },
    loadingClose() {
      if ( this.loadingComponent ) {
        this.loadingComponent.close();
      }
    }
  }
}).$mount('#app')

/**
 * Router decorator to accept language prefix
 */
router.beforeEach( (to, from, next) => {
  let newUrlArray = to.fullPath.split("/");
  // path does not contain language code, add it
  if ( (newUrlArray.length>1 && newUrlArray[1] != vueAppInstance.$store.state.language.code) && newUrlArray.length>0 ) {
    // get urlLanguage from original old "from" url
    let urlLanguageArray = from.fullPath.split("/");
    let urlLanguage = vueAppInstance.$store.state.language.code;
    if (urlLanguageArray.length>1) {
      urlLanguage = urlLanguageArray[1];
    }
    // language change - check if old urlLanguage is same as the one set in store
    if ( urlLanguage!==vueAppInstance.$store.state.language.code ) {
      // if urlLanguage is not valid, return url with correct new language
      newUrlArray[1] = vueAppInstance.$store.state.language.code;
      let newUrl = newUrlArray.join("/");
      return next({path: newUrl});
    }
    // if language was same but url is still missing language
    let newUrl = to.fullPath;
    // fix the router url with right language for case it was not changed correctly
    if ( newUrlArray.length>1 ) {
      newUrlArray[1] = vueAppInstance.$store.state.language.code;
      newUrl = newUrlArray.join("/");
    }
    return next({path: newUrl});
  }
  return next();
});
