<template>
  <div class="container category-list">
    <div class="columns">
      <div class="column">
        <h1 v-if="categoryDetail" class="title is-1">{{ categoryDetail.name[$store.state.language.code] }}</h1>
        <h1 v-else translate="yes" class="title is-1 title-products">Products</h1>
      </div>
    </div>
    <div class="columns">

      <div class="column is-3">
        <nav class="panel" v-if="this.categories.length>0">
          <p class="panel-heading" translate="yes">
            Sub-Categories
          </p>
          <router-link class="panel-block" v-for="(category, i) in this.categories" :key="i"
            :title="category.descriptionShort[$store.state.language.code]" 
            :to="{name: 'categoryList', params: { category: category.pathSlug}}">
            {{ category.name[$store.state.language.code] }}
          </router-link>
        </nav>
        <category-filter ref="categoryFilter" :filter="filter" :categoryMinMaxPrice="categoryMinMax"></category-filter>
      </div>

      <div class="column is-9" v-if="products.length>0">
        <category-paging ref="categoryPaging" :total="categoryProductsTotalForPaging"></category-paging>
        <div class="tile is-ancestor" v-for="(productGroup, i) in products" :key="(groupsLastRefresh+i)">
          <router-link
            v-for="(product, j) in productGroup"
            :key="(groupsLastRefresh+product._id+(i+j))"
            class="tile is-parent is-4"
            :title="product.descriptionShort[$store.state.language.code]" 
            :to="{name: 'productDetail', params: { category: product.activeParentCategoryUrl,  product: product._id }}">
            <product-box :product="product"></product-box>
          </router-link>
        </div> <!-- tile end -->
        <category-paging ref="categoryPaging" :bottom="true" :total="categoryProductsTotalForPaging"></category-paging>
      </div>

    </div>
  </div>
</template>

<script>
import ProductBox from "./ProductBox.vue";
import CategoryFilter from "./CategoryFilter";
import CategoryPaging from "./CategoryPaging";
import { posix } from 'path';
import AppConstants from './../../helpers/constants'

export default {
  name: "CategoryList",
  data () {
    return {
      categories: [],
      filteredProductsCount: null,
      products: [],
      categoryDetail: null,
      filter: {
        minMaxPrice: {
          min: 0,
          max: 0
        },
        stringFilter: "",
        sort: AppConstants.PRODUCT_DEFAULT_SORTING
      },
      language: "en",
      groupsLastRefresh: 0
    }
  },
  computed: {
    categoryMinMax: function() {
      if (this.categoryDetail && typeof this.categoryDetail.minMaxPrice!=="undefined") {
        return this.categoryDetail.minMaxPrice;
      }
      return {
        min: null,
        max: null
      }
    },
    categoryProductsTotalForPaging: function() {
      if (typeof this.filteredProductsCount !== 'undefined' && this.filteredProductsCount!==null) {
        return this.filteredProductsCount;
      } else if (typeof this.categoryDetail.count !== 'undefined' && this.categoryDetail.count) {
        return this.categoryDetail.count;
      } else { 
        return 0; 
      }
    }
  },
  components: {
    'product-box': ProductBox, 
    'category-filter': CategoryFilter, 
    'category-paging': CategoryPaging
  },
  mounted: function () {
    this.listCategoryItems(true);
  },
  methods: {
    processCategoryResponse(response) {
      let nowDate = new Date();
      this.groupsLastRefresh = nowDate.getTime();
      if ( response.data.categoryDetail ) {
        this.categoryDetail = response.data.categoryDetail;
      }
      if ( response.data.categories ) {
        this.categories = response.data.categories;
      }
      if ( response.data.filteredProductsCount ) {
        this.filteredProductsCount = response.data.filteredProductsCount;
      }
      if ( response.data.products ) {
        let products = response.data.products;
        if ( response.data.categoryDetail && response.data.categoryDetail.pathSlug ) {
          products.forEach(function(elem){
            elem.activeParentCategoryUrl = response.data.categoryDetail.pathSlug;
          });
        }
        this.products = this.$store.state.chunk(products, 3);
      }
      if ( response.data.filter ) {
        // store values of previous filter
        let oldFilterStore = Object.assign({}, this.filter);
        // get new value from response
        this.filter = response.data.filter;
        // fill with old values if missing
        let self = this;
        Object.keys(oldFilterStore).forEach(function(key){
          if ( typeof self.filter[key] === "undefined" || self.filter[key]==null ) {
            self.filter[key] = oldFilterStore[key];
          }
        });
      }
      this.$root.loadingClose();
    },

    /**
     * listCategoryItems - get category details and products
     *  1. If filter is in query (after "?") - send POST request to category url WITH filter object.
     *  2. If no filter is available - send GET request to category url.
     */
    listCategoryItems: function(withUrlAnalytics) {
      (typeof withUrlAnalytics === 'undefined' && !withUrlAnalytics) ? withUrlAnalytics = false : withUrlAnalytics = true;
      let self = this;
      if ( this.$route.params && this.$route.params.category && this.$route.params.category.toString().trim()!='' ) {
        this.$root.loadingShow();
        // when requires url analytics (eg. first load) and query has properties (= filter), use filter API
        if (withUrlAnalytics && Object.keys(this.$route.query).length>0 ) {
          // filter is available in url query string, set it to filter variable
          this.queryStringtoFilter();
          self.$httpc.post("products/"+this.$route.params.category, {filter: self.$refs.categoryFilter.buildFilterRequestObject(this.filter)}, { timeout: 7000 })
            .then(function (response) {
              // handle success
              self.processCategoryResponse(response);
              if ( typeof self.$refs.categoryFilter !== "undefined" )  {
                self.$refs.categoryFilter.setTempFilter(self.filter);
              }
            })
            .catch(function (error) {
              // handle error
              console.log(error);
            });
        } else { // no need to analyze query, no filter, just load category using GET
          this.$httpc.get("products/"+this.$route.params.category, { timeout: 7000 })
            .then(function (response) {
              // handle success
              self.processCategoryResponse(response);
            })
            .catch(function (error) {
              // handle error
              console.log(error);
            });
        }
      } else { // no slug
        let typeFilterUrl = this.$route.path;
        let typeFilterUrlArray = typeFilterUrl.split("/");
        if ( typeFilterUrlArray.length>1 ) {
          typeFilterUrlArray.splice(0,2);
          typeFilterUrl = typeFilterUrlArray.join("/");
        }
        let typeFilter = typeFilterUrl;
        this.$httpc.post("categories/find",{
          query: {
            parentPathSlug: "",
            type: typeFilter
          }
        })
          .then(function (response) {
            // handle success
            if ( response.data ) {
              self.categories = response.data;
              // TODO - specific content for products landing page
            }
          })
          .catch(function (error) {
            // handle error
            console.log(error);
          })
          .then(function () {
            // always executed
          });
      }
    }, 

    queryStringtoFilter: function() {
      let filter = {};
      filter['minMaxPrice'] = {};
      // price-min
      if (typeof this.$route.query["price-min"] !== "undefined") {
        let priceMin = parseFloat(this.$route.query["price-min"]);
        if (priceMin>0) {
          filter.minMaxPrice['min'] = priceMin;
        }
      }
      // price-max
      if (typeof this.$route.query["price-max"] !== "undefined") {
        let priceMax = parseFloat(this.$route.query["price-max"]);
        if (priceMax>0) {
          filter.minMaxPrice['max'] = priceMax;
        }
      }
      // string
      if (typeof this.$route.query["string"] !== "undefined") {
        let stringFilter = decodeURIComponent(this.$route.query["string"]).trim();
        if (stringFilter!=="") {
          filter['stringFilter'] = stringFilter;
        }
      }
      // string
      if (typeof this.$route.query["sort"] !== "undefined") {
        let sort = decodeURIComponent(this.$route.query["sort"]).trim();
        if (sort!=="") {
          filter['sort'] = sort;
        }
      }
      this.filter = filter;
    },

    getCategoryMinMax() {
      if (this.categoryDetail && this.categoryDetail.minMaxPrice) {
        return this.categoryDetail.minMaxPrice;
      }
      return null;
    },

    getCategoryFilteredCount() {
      if (this.filteredProductsCount && this.filteredProductsCount>0) {
        return this.filteredProductsCount;
      }
      return 0;
    }

  }
}
</script>

