<template>
  <div class="" v-if="$store.state.user.profile">
    <h2 class="title is-2">Delivery Addresses</h2>
    

    <div class="columns">

      <div class="column is-4">
        
        <nav class="panel user-settings-delivery-nav">
          <p class="panel-heading">
            Addresses
          </p>
          <div>
          <div class="panel-block">
            <p class="control has-icons-left">
              <input class="input is-small" type="text" v-model="deliveryFilter" placeholder="search">
              <span class="icon is-small is-left">
                <i class="fas fa-search" aria-hidden="true"></i>
              </span>
            </p>
          </div>
          <div v-for="(deliveryAddress, d) in userDeliveryAddress" :key="d">
            <a class="panel-block" @click="getDeliveryAddress(d, $event)">
              <span class="panel-icon">
                <a class="fas fa-book" aria-hidden="true" @click.self="removeDeliveryAddress(d)">x</a>
              </span>
              {{deliveryAddress.street}}, {{deliveryAddress.zip}}
            </a>
            </div>
          </div>
          <div class="panel-block">
            <button class="button is-link is-outlined is-fullwidth" @click="addNewDeliveryAddress">
              Add New Delivery Address
            </button>
          </div>
        </nav>
      </div>
      
      <div class="column is-8">

      <form v-on:submit.prevent="saveUserAddresses">
      <div class="deliveryAddressWrapper">
        <div class="columns is-variable is-2">
          <div class="column is-6">
            <div class="field">
              <label class="label">First Name</label>
              <div class="control">
                <input class="input" type="text" v-model="activeDeliveryAddress.nameFirst" placeholder="First Name">
              </div>
            </div>
          </div>
          <div class="column is-6">
            <div class="field">
              <label class="label">Last Name</label>
              <div class="control">
                <input class="input" type="text" v-model="activeDeliveryAddress.nameLast" placeholder="Last Name">
              </div>
            </div>
          </div>
        </div>

        <div class="columns is-variable is-2">
          <div class="column is-12">
            <div class="field">
              <label class="label">Street</label>
              <div class="control">
                <input class="input" type="text" v-model="activeDeliveryAddress.street" placeholder="Street">
              </div>
            </div>
          </div>
        </div>

        <div class="columns is-variable is-2">
          <div class="column is-12">
            <div class="field">
              <label class="label">Street 2</label>
              <div class="control">
                <input class="input" type="text" v-model="activeDeliveryAddress.street2" placeholder="Street 2">
              </div>
            </div>
          </div>
        </div>

        <div class="columns is-variable is-2">
          <div class="column is-4">
            <div class="field">
              <label class="label">ZIP</label>
              <div class="control">
                <input class="input" type="text" v-model="activeDeliveryAddress.zip" placeholder="ZIP">
              </div>
            </div>
          </div>
          <div class="column is-8">
            <div class="field">
              <label class="label">City</label>
              <div class="control">
                <input class="input" type="text" v-model="activeDeliveryAddress.city" placeholder="City">
              </div>
            </div>
          </div>
        </div>

        <div class="columns is-variable is-2">
          <div class="column is-6">
            <div class="field">
              <label class="label">State</label>
              <div class="control">
                <input class="input" type="text" v-model="activeDeliveryAddress.state" placeholder="State">
              </div>
            </div>
          </div>
          <div class="column is-6">
            <div class="field">
              <label class="label">Country</label>
              <div class="control">
                <input class="input" type="text" v-model="activeDeliveryAddress.country" placeholder="Country">
              </div>
            </div>
          </div>
        </div>

        <div class="columns is-variable is-2 is-centered">
          <div class="column is-6">
            <div class="field">
              <label class="label">Phone</label>
              <div class="control">
                <input class="input" type="text" v-model="activeDeliveryAddress.phone" placeholder="Phone">
              </div>
            </div>
          </div>
        </div>

        <div class="columns is-variable is-2 is-centered">
          <div class="column is-6 has-text-centered">
            <button type="submit" class="button is-success" @click="saveUserAddresses">
              <span class="icon">
                <i class="fas fa-home"></i>
              </span>
              Save Address
            </button>
          </div>
        </div>
      </div>
      </form>

    </div></div>


  </div>
</template>

<script>
export default {
  name: 'UserMeDelivery',
  data () {
    return {
      activeDeliveryAddressIndex: 0,
      activeDeliveryAddress: {
        "type":"delivery",
        "nameFirst":"",
        "nameLast":"",
        "street":"",
        "street2":"",
        "zip":"",
        "city":"",
        "country":"",
        "phone":""
      },
      deliveryFilter: ''
    }
  },
  computed: {
    userDeliveryAddress: function() {
      let userAddressDelivery = this.$store.getters.getUserProfile.addressesDelivery;
      let result = [];

      if ( userAddressDelivery && userAddressDelivery.length>0 ) {
        result = userAddressDelivery;

        // if possible, apply filter
        if ( this.deliveryFilter.toString().trim()!=='' ) {
          let searchPhrase = this.deliveryFilter.toString().trim().toLowerCase();
          let searchResults = [];
          userAddressDelivery.forEach( (element, index) => {
            let foundInElement = false;
            // loop element properties to check for searchPhrase
            for (var property in element) {
              if (element.hasOwnProperty(property)) {
                // check for searchPhrase
                // console.log( "in " + property + " value (" + element[property] + ") found " + element[property].indexOf(searchPhrase) + " times" );
                if ( element[property].toString().trim().toLowerCase().indexOf(searchPhrase)>-1 ) {
                  foundInElement = true;
                }
              }
            }
            // if found, add to searchResults
            if ( foundInElement ) {
              searchResults.push(element);
            }
          });
          // if any results, replace original
          if ( searchResults.length>0 ) {
            result = searchResults;
          }
        }
      }

      console.log(result.length);
      return result;
    }
  },
  methods: {
    getDeliveryAddress: function(index, event) {
      this.activeDeliveryAddressIndex = index;
      this.activeDeliveryAddress = this.userDeliveryAddress[index];

      let listItems = this.$el.querySelectorAll('.users-me nav.user-settings-delivery-nav a.panel-block');
      if ( listItems && listItems.length ) {
        listItems.forEach(element => {
          element.classList.remove('is-active');
        });
      }

      event.target.classList.add("is-active");
    },
    addNewDeliveryAddress: function() {
      let self = this;
      // TODO - check if same address does not exist
      this.$store.dispatch("addNewDeliveryAddress", {
        "type":"delivery",
        "nameFirst":"",
        "nameLast":"",
        "street":"",
        "street2":"",
        "zip":"",
        "city":"",
        "country":"",
        "phone":""
      })
      .then(() => {
        self.$forceUpdate();
      });
      this.$forceUpdate();
    },
    removeDeliveryAddress: function(index) {
      let self = this;
      this.$store.dispatch("removeDeliveryAddress", index )
      .then(() => {
        self.activeDeliveryAddressIndex = 0;
        self.$forceUpdate();
        self.saveUserAddresses();
      });
    },
    saveUserAddresses: function() {
      let addresses = [];
      let userProfile = this.$store.getters.getUserProfile

      addresses.push(userProfile.addressInvoice);
      if ( userProfile.addressesDelivery.length>0 ) {
        for (let i=0; i<userProfile.addressesDelivery.length; i++) {
          addresses.push(userProfile.addressesDelivery[i]);
        }
      }
      this.$store.dispatch("updateUser", { "addresses": addresses } );
    }
  }
}
</script>
